import { useContext } from 'react';
import { Web3Context } from '../../contexts/Web3Context';
import Button from './Button';

export default function ConnectWrapper(props) {
    const {signedInAddress, connect} = useContext(Web3Context)

    if (signedInAddress && signedInAddress.length) {
        return (
            {...props.children}
        )
    } else {
        return (
            <Button className="button" onClick={connect}>{props.connectText || 'Connect'}</Button>
        )
    }
    
}
