import Button from "./widgets/Button";
import Input from "./widgets/Input";
import DatePicker from "react-datepicker";
import { useContext, useEffect, useState } from "react";
import { FileUploader } from "react-drag-drop-files";
import "react-datepicker/dist/react-datepicker.css";
import 'react-datepicker/dist/react-datepicker-cssmodules.css';
import SelectField from "./widgets/SelectField";
import { useForm, Controller } from "react-hook-form";
import { Web3Context } from "../contexts/Web3Context";
import axios from "axios";
import Spinner from "./widgets/Spinner";

const TextInput = (props) => {
    const onChange = (ev) => {
        if (props.onChange) {
            props.onChange(ev.target.value);
        }
    }

    return <div style={{display: 'flex', flexDirection: 'column'}}>
        <label>{props.label}</label>
        <input 
            name={props.name}
            type="text" 
            onChange={onChange}
            value={props.value || ""}></input>
    </div>
}

function NFTCalendarFormPageContainer(props) {
    return (
        <div className="page">
            <h1>List NFT project<span>.</span></h1>
            {props.children}
            </div>
    )
}

export default function NFTCalendarForm() {

    const { web3 } = useContext(Web3Context);

    const [imageFile, setImageFile] = useState(null);
    const [imageFileSrc, setImageFileSrc] = useState(null);
    const [validationErrors, setValidationErrors] = useState<any>({});
    const [status, setStatus] = useState("");
    
    const [formValues, setFormValues] = useState<any>({
        title: "",
        description: "",
        date: new Date(),
        mintPrice: "",
        totalSupply: "",
        contract: "",
        name: "",
        url_web: "",
        url_twitter: "",
        url_discord: "",
        url_telegram: ""
    });

    const validateForm = () => {
        const validateString = (key, errors, minWords) => {
            if (formValues[key].length == 0) {
                errors[key] = "Can't be empty"
            } else {
                const numWords = formValues[key].split(" ").length;
                if (numWords < minWords) {
                    errors[key] = "Neet to be at least " + minWords + " words."
                }
            }
        }

        const validateUrl = (key, errors) => {
            function validURL(str) {
                var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
                  '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
                  '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
                  '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
                  '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
                  '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
                return !!pattern.test(str);
            }
            const value = formValues[key];
            if (value && value.length > 0 && !validURL(value)) {
                errors[key] = "Invalid URL";
            }
        }

        let errors: any = {};

        validateString("title", errors, 1);
        validateString("description", errors, 10);
        
        if (!formValues.image || formValues.image.length == 0) {
            errors.image = "Image is required"
        }

        if (formValues.contract && formValues.contract.length > 0) {
            if (!web3?.utils.isAddress(formValues.contract)) {
                errors.contract = "Invalid contract address";
            }
        }

        if (formValues.totalSupply.length > 0 && /^\d+$/.test(formValues.totalSupply) == false) {
            errors.totalSupply = "Not a valid number";
        }
        
        if (formValues.mintPrice.length > 0 && /^\d*\.?\d*$/.test(formValues.mintPrice) == false) {
            errors.mintPrice = "Not a valid decimal number";
        } 
        
        validateUrl("url_web", errors);
        validateUrl("url_twitter", errors);
        validateUrl("url_discord", errors);
        validateUrl("url_telegram", errors);

        setValidationErrors(errors);
    }

    useEffect(validateForm, [formValues])
    

    const setFormValue = (key) => {
        return (newValue) => {
            setFormValues({
                ...formValues,
                [key]: newValue
            })
        }
    }


    const handleImageFileChange = (file) => {
        if (file.size > 1024*1024) {
            return;
        }

        setImageFile(file);

        var reader = new FileReader();
        reader.onload = function (e: any) {
            setImageFileSrc(e.target.result);
            setFormValue("image")(e.target.result);
        };

        reader.readAsDataURL(file);
    };
    
    async function submitForm(event) {
        setStatus("submitting");

        event.preventDefault();
        
        try {
            const res = await axios.post("https://1crfk34q6g.execute-api.us-east-1.amazonaws.com/beta/nftdrops", formValues)
            
            setTimeout(() => {
                if (res.status === 200) {
                    setStatus("submitted");
                } else {
                    setStatus("error");
                }
            }, 500);    
        } catch (e) {
            setStatus("error");
        }
    }

    if (status == "submitting") {
        return (
            <NFTCalendarFormPageContainer>
                <h2>Submitting details...</h2>
                <Spinner></Spinner>
            </NFTCalendarFormPageContainer>
        )
    }
    else if (status == "submitted") {
        return (
            <NFTCalendarFormPageContainer>
            <h2>Thank you!</h2>
            <div>Your submission will be reviewed shortly.</div>
            <br></br>
            <a href="/nftdrops"><i className="fas fa-caret-left"></i> Back to calendar view</a>
            </NFTCalendarFormPageContainer>           
        )
    }

    return (
        <NFTCalendarFormPageContainer>

            <form className="nftdrop-form" 
                onSubmit = { submitForm }
                action = "#"
                
            >
                                
                                { /* 
                                method="POST" 
                action="http://localhost:3010/test"
                                action="https://1crfk34q6g.execute-api.us-east-1.amazonaws.com/beta/nftdrops" */ }

                <h3>Project info</h3>
                
                <TextInput name="title" label="*Project title" value={formValues.title} onChange={setFormValue("title")} />
                <div className="nftdrop-validation-error">{validationErrors.title}</div>

                <label>*Cover image <span>Square image, max 1MB</span></label>
                <div className="image-upload-component">
                <FileUploader maxSize={1} handleChange={handleImageFileChange} name="filename" types={["JPG", "PNG", "GIF", "WEBP"]} />
                { imageFileSrc ? <img className="image-preview" src={imageFileSrc} /> : null }
                </div>
                <input type="hidden" name="image" value={imageFileSrc || ""}></input>
                <div className="nftdrop-validation-error">{validationErrors.image}</div>
                
                <label>*Description <span>Brief description around 30-50 words</span></label>
                <textarea name="description" value={formValues.description} rows={4} onChange={(event) => { setFormValue("description")(event.target.value) }}></textarea>
                <div className="nftdrop-validation-error">{validationErrors.description}</div>

                <label>*Mint date</label>
                <DatePicker name="date" selected={formValues.date} onChange={setFormValue("date")} />
                <div className="nftdrop-validation-error">{validationErrors.date}</div>

                <input type="text" id="name" name="name" value="" tabIndex={-1} autoComplete="off"></input>

                <div className="two-columns">
                    <div>
                        <TextInput name="num_tokens" id="num-tokens" type="int" label="Total supply" value={formValues.totalSupply} onChange={setFormValue("totalSupply")}/>
                        <div className="nftdrop-validation-error">{validationErrors.totalSupply}</div>
                    </div>
                    <div>
                        <TextInput name="mint_price" id="mint-price" type="decimal" label="Mint price (in BNB)" value={formValues.mintPrice} onChange={setFormValue("mintPrice")}/>
                        <div className="nftdrop-validation-error">{validationErrors.mintPrice}</div>
                    </div>
                </div>

                <TextInput name="contract" id="contract" type="contract" label="NFT contract address" value={formValues.contract} onChange={setFormValue("contract")}/>
                <div className="nftdrop-validation-error">{validationErrors.contract}</div>


                <br></br>
                <h3>Links</h3>
                <TextInput name="url_website" label="Website" type="url" value={formValues.url_web} onChange={setFormValue("url_web")}/>
                <div className="nftdrop-validation-error">{validationErrors.url_web}</div>
                <TextInput name="url_twitter" label="Twitter" type="url" value={formValues.url_twitter} onChange={setFormValue("url_twitter")}/>
                <div className="nftdrop-validation-error">{validationErrors.url_twitter}</div>
                <TextInput name="url_discord" label="Discord" type="url" value={formValues.url_discord} onChange={setFormValue("url_discord")}/>
                <div className="nftdrop-validation-error">{validationErrors.url_discord}</div>
                <TextInput name="url_telegram" label="Telegram" type="url" value={formValues.url_telegram} onChange={setFormValue("url_telegram")}/>
                <div className="nftdrop-validation-error">{validationErrors.url_telegram}</div>

                <br></br>
                <Button className={Object.keys(validationErrors).length > 0 ? "button--disabled" : ""}>Submit</Button>
                {Object.keys(validationErrors).length > 0 ? (
                    <div className="nftdrop-validation-error">From has errors</div>
                ) : null}
                {status == "error" ? (
                    <div className="nftdrop-validation-error">There was an error submitting. Please check form details or try again later.</div>
                ) : null}
            </form>
        </NFTCalendarFormPageContainer>
    )

}

