export default function Button(props) {
    const onClick = () => {
        if (!props.onClick) {
            return
        }
        setTimeout(() => {
            props.onClick();
        }, 100)
    }
    return (<button onClick={onClick} className={"button " + props.className || ''}>
        <span className="button-shadow"></span>
        <span className="button-edge"></span>
        <span className="button-front">
            {props.children}
        </span>     
    </button>)
}