import SocialLinks from "../SocialLinks";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import { useState } from "react";

export default function Footer() {
    const [copied, setCopied] = useState(false);

    const copyClipboard = () => {
        setCopied(true);
        setTimeout(() => {
            setCopied(false);
        }, 1500)
    }

    return (
        <div className="footer">

            <div className="footer-items">
                <div className="footer-about">
                    <img className="shitpunks-logo-footer" src="/images/logo.png"/>
                    <div className="footer-text">
                        <h3 className="footer-header">About The Project</h3>
                        ShitPunks is a collection of 10,000 hand-drawn punks on the Binance Smart Chain based off the infamous CryptoPunks collection.

                        <div className="footer-contract"><span>Contract:</span>
                            &nbsp;<a rel="noreferrer" target="_blank" href="https://bscscan.com/address/0x11304895f41c5a9b7fbfb0c4b011a92f1020ef96">0x1130...F96</a>
                            &nbsp;<CopyToClipboard text="0x11304895f41C5A9b7fBFb0C4B011A92f1020EF96">
                            <span onClick={copyClipboard} className="copy-clipboard-icon"><i className="fas fa-clipboard"></i></span>
                            </CopyToClipboard>
                            { copied ? <span className="copied-text">Copied!</span> : null}
                        </div>
                    </div>
                </div>

                <div className="footer-social">
                    <h3 className="footer-header">Social media</h3>
                    <SocialLinks ></SocialLinks>
                </div>               
            </div>

            <div className="footer-copyright">
                Copyright  2021 Shitpunks LLC, All Rights Reserved.
                Not affiliated with Larva Labs or CryptoPunks.
            </div>
            
        </div>
    )
}