import SelectField from "./widgets/SelectField";
import { useSearchParams } from "react-router-dom";
import IconButton from "./widgets/IconButton";

export default function PunkSearchBar(props) {

    const [searchParams, setSearchParams] = useSearchParams();

    const priceSortOptions = [
        {
            label: 'Price: Low to high',
            value: 'price-low'
        },
        {
            label: 'Price: High to low',
            value: 'price-high'
        }
    ]

    const rankSortOptions = [
        {
            label: 'Rank: Low to high',
            value: 'rank-low'
        },
        {
            label: 'Rank: High to low',
            value: 'rank-high'
        }
    ]

    const modeSpecificSortOptions = {
        activity: [
            {
                label: "Recently sold",
                value: 'time'
            },
            ...priceSortOptions,
            ...rankSortOptions
        ],
        forsale: [
            {
                label: "Recently listed",
                value: 'time'
            },
            ...priceSortOptions,
            ...rankSortOptions
        ],
        all: [
            ...rankSortOptions,
            {
                label: 'Token id: Low to high',
                value: 'tokenId-low'
            },
            {
                label: 'Token id: High to low',
                value: 'tokenId-high'
            }
        ]
    }

    const sortOptions = props.sortOptions || [
        ...modeSpecificSortOptions[props.marketplaceMode],
    ]

    const filters = ["Type", "Hat-Hair", "Eyes", "Ears", "Mouth", "Mouth Accessory", "Beard", "Face", "Nose", "Lip", "Neck", "rank"]
    let numFilters = 0;
    for (const [key, value] of searchParams) {
        if (filters.includes(key)) {
            numFilters++;
        }
    }

    // price=high/low

    //const className = `punk-search-bar ` + (props.visible ? "punk-search-bar--visible" : "")

    const sortChanged = (event) => {
        let params = searchParams;
        if (event.value == null ||
            event.value == 'time') {
            params.delete('sort')
        } else {
            params.set('sort', event.value);
        }
        
        setSearchParams(params, { replace: false })
    }

    const getDefaultSort = () => {
        if (props.defaultSort) {
            return props.defaultSort;
        }
        if (props.marketplaceMode == "all") {
            return sortOptions[1];
        }
        return sortOptions[0];
    }

    const selectedSort = searchParams.get('sort')
    const sortDefaultValue = sortOptions.find(opt => opt.value == selectedSort) || getDefaultSort();
    
    return (
        <div className="punk-search-bar">
            <span style={{display: "flex", gap: "1rem"}}>
            <IconButton className="filter-button" iconClass="fas fa-filter" onClick={props.showFilters}>
                { numFilters > 0 ? (
                    <div className="filters-button-badge">{numFilters}</div>
                ) : null}
            </IconButton>
            <IconButton className="" iconClass="fas fa-sync" onClick={props.refresh}/>
            </span>

            <SelectField
                name="sort" 
                options={sortOptions}
                defaultValue={sortDefaultValue}
                onChange={sortChanged}
            ></SelectField>
        </div>
    )
}
