import { useContext, useRef, useState } from "react";
import { Web3Context } from "../../contexts/Web3Context";
import Popup from 'reactjs-popup'
import { ClaimRewardsModal } from "./ClaimRewardsModal";
import Button from "../widgets/Button";

export function WalletModal(props) {
    const {signedInAddress, disconnect} = useContext(Web3Context)
    let [didCopyAddress, setDidCopyAddress] = useState(false)

    const copyToClipboard = () => {
        setDidCopyAddress(true)
        navigator.clipboard.writeText(signedInAddress);
        setTimeout(props.close, 400);
    }

    const ref: any = useRef();

    return (
        <div className="wallet-popup">
            <Button className="button button button-wallet" onClick={copyToClipboard}>
                &nbsp;{didCopyAddress ? <>Copied!</> : 
                (
                    <><i className="fas fa-clipboard" />&nbsp;Copy address</>
                )}
            </Button>
            <ClaimRewardsModal closeParent={props.close}>
                <Button className="button button button-wallet" onClick={() => {
                    console.log("closing parent")
                }}>
                    <i className="fas fa-hand-holding-usd" />&nbsp;Claim rewards
                </Button>
            </ClaimRewardsModal>
            
            <Button className="button button button-wallet button--danger" onClick={disconnect}>
                <i className="fas fa-sign-out-alt" />&nbsp;Disconnect
            </Button>
        </div>
        )
}

export function WalletModalTrigger(props: any) {
    return (
        <Popup  
            nested
            lockScroll={true}
            position="bottom right"
            trigger={
                ({...props.children})
            }> 
            {(close: any) => (<WalletModal close={close}/>)
        }
    </Popup>)
}

export default function Wallet() {
    const {signedInAddress, signedInAddressShort, connect} = useContext(Web3Context)
 
    if (signedInAddress.length > 0) {
        return (
            <WalletModalTrigger>
                <div>
                <Button className="button button-wallet button"
                >{signedInAddressShort}</Button>
                </div>
                
            </WalletModalTrigger>
            
        )
    } else {
        return (
            <Button className="button button-wallet"
                onClick={connect}
                >Connect wallet</Button>
        )
    }
    
}