import { useCallback } from "react";
import { useContext } from "react";
import { Web3Context } from "../contexts/Web3Context";

export default function useAddressUtils() {

    const {signedInAddress, contracts} = useContext(Web3Context)

    const walletLink = (address) => {
        return (
            <a className="wallet-link" href={`/collection?wallet=${address}`}><i className="fas fa-wallet"></i></a>
        )
    }

    const formatDate = useCallback((dateString: string) => {
        return new Date(dateString).toLocaleDateString('en-GB', {
            day : 'numeric',
            month : 'short',
            year : 'numeric'
        })
    }, [])

    const formatAddress2 = useCallback((address, mini: boolean) => {
        if (address == "0x0000000000000000000000000000000000000000") {
            return "0";
        }

        if (signedInAddress && address && address.toLowerCase() == signedInAddress.toLowerCase()) {
            return "You"
        }

        if (address == "0xFa7ce4Bc043a5706c4a706B12b63b27e441d7897") {
            return "Shitpunks (old)"
        } else if (address == "0x11304895f41C5A9b7fBFb0C4B011A92f1020EF96") {
            return "Shitpunks"
        } else if (address == "0x17539cCa21C7933Df5c980172d22659B8C345C5A") {
            return "PancakeSwap"
        }

        if (!address) {
            return ""
        }

        if (mini) {
            return address.substr(0,6);
        } else {
            return address.substr(0,6) + "..." + address.substr(address.length-3);
        }
        
    }, [signedInAddress])

    const formatAddressMini = useCallback(address => formatAddress2(address, true), []);
    const formatAddress = useCallback(address => formatAddress2(address, false), []);

    const addressLink = useCallback((address) => {
        return `https://bscscan.com/address/${address}`
    }, [])

    const txLink = useCallback((address) => {
        return `https://bscscan.com/tx/${address}`
    }, [])

    return {
        formatDate,
        formatAddress,
        formatAddressMini,
        addressLink,
        txLink,
        walletLink
    }
}
