import Popup from "reactjs-popup";
import { useContext, useEffect, useState } from "react";
import { Web3Context } from '../contexts/Web3Context';
import useAddressUtils from "../hooks/AddressUtils";
import Input from "../components/widgets/Input";
import Spinner from "../components/widgets/Spinner";
import { TransactionConfig } from 'web3-core'
import Button from "../components/widgets/Button";

function ListStep_WrapToken(props) {

    const { addressLink } = useAddressUtils()
    const [ waiting, setWaiting ] = useState(false)
    const [ state, setState ] = useState<any>({})

    const wrapToken = async () => {
        setWaiting(true);


        const result = await props.writeContract(props.contracts.shitpunks, 'safeTransferFrom', [
            props.signedInAddress, 
            props.contracts.shitpunks2.address,
            props.tokenId
        ]);

        setState(result);
        setWaiting(false);
    }

    const render = () => {
        if (waiting) {
            return (
                <>
                Wrapping token...
                <Spinner></Spinner>
                </>
            )
        } else {
            return (
                <>
                Token needs to be wrapped with the new contract to allow listing.
                { state.error ? (<div className='error-text'>{state.error}</div>) : null}
                <Button onClick={wrapToken} className="button">Wrap token</Button>

                <div className="action-description">
                    {props.metadata.name} is still on the <a href={addressLink(props.contracts.shitpunks.address)} rel="noreferrer" target="_blank">old</a> ShitPunks contract. 
                    Wrapping the token will send it to the <a href={addressLink(props.contracts.shitpunks2.address)} rel="noreferrer" target="_blank">new</a> ShitPunks contract. You will be given a new copy of the token back that has extended functionality.
                </div>
                </>
            )
        }
    }

    return (
        <>
            

            {render()}
            
        </>
    )
}

function ListStep_ApproveContract(props) {

    const [state, setState] = useState<any>({
        state: ""
    })

    const startApproval = async () => {
        setState({
            state: "approving"
        })

        const contractJson = props.contracts.shitpunks2;

        try {

            const contract = new props.web3.eth.Contract(contractJson.abi, contractJson.address)
            const tx = await contract.methods.approve(props.contracts.shitmarket.address, props.tokenId);
            const data = tx.encodeABI();
            const gas = await tx.estimateGas({from: props.signedInAddress});
            const gasPrice = await props.web3.eth.getGasPrice();       
            const transcationConfig: TransactionConfig = {
                from: props.signedInAddress,
                to: contractJson.address,
                gas: gas,
                gasPrice: gasPrice,
                data: data,
                chainId: 0x38,
            }

            const receipt = await props.web3.eth.sendTransaction(transcationConfig);       
            setState({
                state: "done"
            })

            props.onSuccess();

        } catch (e: any) {
            console.log("Failed", e);
            setState({
                state: "failed",
                message: e.message
            })
        }
    }

    if (state.state == "done") {
        return (<>
        </>)
    }
    
    const getStateUI = () => {
        if (state.state == "approving") {
            return (<div style={{margin: "2rem"}}><Spinner></Spinner></div>)
        } else {
            if (state.state == "failed") {
                return (
                    <>
                    <div className="error-text">{state.message}</div>
                    <Button className="button" onClick={startApproval}>Try again</Button>
                    </>
                )
            } else {
                return (<Button className="button" onClick={startApproval}>Approve contract</Button>)
            }
        }
    }

    return (
        <>
            ShitMarket needs approval to manage your token.

            { getStateUI() }
            
            <div className="action-description">
                Gives permission to ShitMarket to transfer {props.metadata.name} to another account. The approval is cleared when the token is transferred.
            This is part of the ERC 721 standard. (<a href="https://docs.openzeppelin.com/contracts/3.x/api/token/erc721#IERC721-approve-address-uint256-" rel="noreferrer" target="_blank">Read more</a>)
            </div>
        </>
    )
}

function ListStep_ListToken(props) {

    const [price, setPrice] = useState("1")
    const [state, setState] = useState<any>({
        state: ""
    })

    const listToken = async () => {
        setState({
            state: "listing"
        })

        try {
            const priceInWei = props.web3.utils.toWei(price, 'ether')
            const results = await props.writeContract(props.contracts.shitmarket, 'createListing', [props.tokenId, priceInWei]);

            setState({
                state: "done",
                receipt: results.receipt
            })
        } catch (e: any) {
            setState({
                state: "failed",
                message: e.message
            })
        }   
    }

    const render = () => {
        if (state.state == "listing") {
            return <Spinner></Spinner>
        } else if (state.state == "done") {
            return (<>
                <div>{props.metadata.name} has been listed for {price} BNB.</div>
                <div>View <a rel="noreferrer" target="_blank" href={props.txLink(state.receipt.transactionHash)}>transaction here</a>.</div>
                <Button onClick={() => {
                    props.onSuccess();
                    props.close()
                }} className="button button button-ok">Ok</Button>
            </>)
        } else {
            return (<>
            <div className="price-input-container">
                <Input label="Price" className="price-input" type="number" value={price} onChange={setPrice}></Input> 
                <div style={{marginTop: "2rem"}}>BNB</div>

                <Button onClick={listToken} style={{marginTop: "2rem", marginLeft: "2rem"}} className="button">List for sale</Button>
            </div>
            </>)
        }
    }

    return (
        <>
            {render()}
            
        </>
    )
}


export default function ListOnMarketModal(props) {

    const {signedInAddress, web3, contracts, writeContract} = useContext(Web3Context)
    const [isWrapped, setWrapped] = useState(false)
    const [isApproved, setApproved] = useState(false)
    const [isLoading, setLoading] = useState(true)
    const {txLink} = useAddressUtils();

    const checkWrapped = async () => {
        setLoading(true)
        if (!web3) {
            return;
        }
        
        const contract = new web3.eth.Contract(contracts.shitpunks.abi, contracts.shitpunks.address)
        const owner = await contract.methods.ownerOf(props.tokenId).call()
        const isWrapped = owner === contracts.shitpunks2.address;
        setWrapped(isWrapped);
        setLoading(false)
    }

    useEffect(() => {
        checkWrapped();
    }, [web3])

    const checkApproval = async () => {
        setLoading(true)
        if (!web3) {
            return;
        }
        
        const contract = new web3.eth.Contract(contracts.shitpunks2.abi, contracts.shitpunks2.address)
        const approvedAddress = await contract.methods.getApproved(props.tokenId).call()
        const isApproved = approvedAddress === contracts.shitmarket.address;
        setApproved(isApproved);
        setLoading(false)
    }

    useEffect(() => {
        checkApproval();
    }, [isWrapped])

    const getStep = (close: any) => {
        if (isLoading) {
            return <Spinner></Spinner>
        }
        if (!isWrapped) {
            return (<ListStep_WrapToken {...props} signedInAddress={signedInAddress} contracts={contracts} writeContract={writeContract}
                onSuccess={checkWrapped}
            ></ListStep_WrapToken>)
        } else if (!isApproved) {
            return (<ListStep_ApproveContract {...props} web3={web3} signedInAddress={signedInAddress} contracts={contracts}
                onSuccess={checkApproval}
                ></ListStep_ApproveContract>)
        } else {
            return (<ListStep_ListToken 
                    close={close} 
                    onSuccess={props.onChange} 
                    txLink={txLink} 
                    {...props} 
                    web3={web3} 
                    signedInAddress={signedInAddress} 
                    contracts={contracts}
                    writeContract={writeContract}
                ></ListStep_ListToken>)
        }
    }

    return (
        <Popup
            nested
            modal
            trigger={
                ({...props.children})
            }>
            {(close: any) => (<div className="modal list-market-modal">   
                    <Button className="button--modal-close" onClick={close}>&times;</Button>
                    <h3>List token on ShitMarket</h3>
                    <div className="content">
                        { getStep(close) }
                    </div>
            </div>)
            }
        </Popup>
    )
}
