import { useContext, useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { Web3Context } from "../contexts/Web3Context";
import Button from "./widgets/Button";
import axios from 'axios';
import Spinner from "./widgets/Spinner";
import ConnectWrapper from "./widgets/ConnectWrapper";

const getDiscordAuthUri = (action: string) => {
    const clientId = "927305299466018836";
    
    const redirectUri = `${window.location.origin}/collection?diamondhands=${action}`;
    return `https://discord.com/api/oauth2/authorize?client_id=${clientId}&redirect_uri=${redirectUri}&response_type=code&scope=identify`;
}

const connectDiscord = (action: string) => {
    window.location.href = getDiscordAuthUri(action);
}

function DiamondHandsCriterias(props) {
    const { criterias } = props.status;
    criterias[0].description = "Link wallet with Discord id by clicking the button"
    criterias[2].description = "All ShitPunks unlisted or listed at 0.5 or above"
    return (
        <div className="diamond-criterias">
            { criterias.map((criteria, index) => {
                return (
                    <div className="criteria">
                        { criteria.value ? (
                            <i className="criteria-icon fas fa-check-circle"></i>
                        ) : (
                            <i className="criteria-icon fas fa-times-circle"></i>
                        )}
                        
                        <div className="criteria-description">
                        {criteria.description} {index == 0 ? <span className="dmd-button-right"></span> : null}
                        </div>
                        
                    </div>
                )
            }) }
        </div>
        
    )
}

function DiamondHandsExtraInfo(props) {
    const [isVisible, setVisible] = useState(!props.status.linked);

    return (
        <div className="diamond-hands-extra-info">
            <div className="diamond-hands-title text-accent"><i className="fas fa-gem"></i>&nbsp;
                { props.status.linked ? (
                    <>You have got</>
                ) : (
                    <>You can apply for</>
                ) }
            &nbsp;
                <div className={`read-more-toggle ${isVisible ? "read-more-toggle--visible" : ""}`} onClick={() => { setVisible(!isVisible) }}>
                    Diamond Hands!
                </div>
            </div>
            
            <div className={`read-more-contents ${isVisible ? "read-more-contents--visible" : ""}`}>
                The Diamond Hands program is a special designation for our most loyal holders with 
                the goal of continually raising the floor for our collection and providing additional 
                value to our holders. Entry into the program requires a listing price of no lower 
                than 0.5 BNB on any ShitPunk in your wallet. 
                <br></br><br></br>
                Admission to the program provides a special role in <a target="_blank" href="https://discord.gg/ShitPunks">Discord</a>, as well as entry in a lottery drawing for monthly airdrops.
                <br/>
                { props.status.linked ? (
                    <a href={getDiscordAuthUri('leave')}>
                        Leave Diamonds Hands Program
                    </a>
                ) : null}
                
            </div>
        </div>
    )
}


export function DiamondHands(props) {   

    const diamondHandsApi = "https://acy9375pq6.execute-api.us-east-1.amazonaws.com/diamondhands";

    const [searchParams, setSearchParams] = useSearchParams();
    const {signedInAddress} = useContext(Web3Context)
    const [fetchStatusSalt, setFetchStatusSalt] = useState(0);
    const [linkStatus, setLinkStatus] = useState<any>({
        pending: true
    });

    const hasWallet = signedInAddress.length > 0;

    useEffect(() => {
        if (!hasWallet) {
            return;
        }
        
        fetch(`${diamondHandsApi}?action=status&wallet=${signedInAddress}`).then(async res => {
            const json = await res.json();
            if (json.pending) {
                setTimeout(() => {
                    setFetchStatusSalt(fetchStatusSalt + 1);
                }, 1000);
            }

            console.log("setting status", json);
            setLinkStatus(json.status);
        })

    }, [hasWallet, signedInAddress, fetchStatusSalt])

    const discordCode = searchParams.get('code');
    const diamondhandsAction = searchParams.get('diamondhands');

    useEffect(() => {
        if (!discordCode || !hasWallet) {
            return;
        }

        const apiFunc = diamondhandsAction == "register" ? "register" : "leave";
        const apiUri = `${diamondHandsApi}?action=${apiFunc}&code=${discordCode}&wallet=${signedInAddress}`;
        axios.get(apiUri).then(res => {
            console.log("Register res", res);
        })
    }, [discordCode, signedInAddress, hasWallet, diamondhandsAction])

    if (!hasWallet) {
        return (
            <ConnectWrapper connectText="Connect wallet"></ConnectWrapper>
        )
    } else if (linkStatus.pending) {
        return (
            <div className="diamond-hands-container">
                <Spinner></Spinner>
            </div>
        )
    } else if (false && linkStatus.linked) {
        return (
            <div className="diamond-hands-container">
                <DiamondHandsExtraInfo status={linkStatus} connectDiscord={connectDiscord}></DiamondHandsExtraInfo>
                { linkStatus.criterias ? (
                    <DiamondHandsCriterias status={linkStatus}></DiamondHandsCriterias>
                ) : null }
            </div>
        )
    } else {
        return (
            <div className="diamond-hands-container">
                <DiamondHandsExtraInfo 
                    status={linkStatus}
                ></DiamondHandsExtraInfo>
                { linkStatus.criterias ? (
                    <DiamondHandsCriterias status={linkStatus}></DiamondHandsCriterias>
                ) : null }
                <Button className="button"
                    onClick={() => { connectDiscord("register") }}
                >
                    Join Diamonds Hands program
                </Button>
            </div>
        )
    }

}
