import { useEffect, useState } from 'react';
import { Random, MersenneTwister19937 } from 'random-js'
import SpeechBubble from './SpeechBubble';
import VisibilitySensor from 'react-visibility-sensor'
import Spinner from './widgets/Spinner';

const getRarityImage = (rank) => {
    if (rank < 100) {
        return "legendary"
    } else if (rank < 1000) {
        return "epic"
    } else if (rank < 2500) {
        return "rare"
    } else if (rank < 5000) {
        return "uncommon"
    } else {
        return "common"
    } 
}

const SpeechBubbles = (props) => {
    if (!props.isVisible) {
        return (<></>)
    }

    const phrases = [
        "ShitPunks to the moon!",
        "Shit Happens!",
        "Shit just got real!",
        "Youre THE SHIT!",
        "I'm as cool as shit!",
        "Watch out! Shits about to hit the fan!"
    ]
    
    return (
        <div className="speech-bubbles">
            {[...Array(7)].map((x, index) => {
                return (
                    <SpeechBubble key={"bubble"+index} arrowPosition={.8} classSuffix={"punk" + (index+1)}>
                        {phrases[index % phrases.length]}
                    </SpeechBubble>
                )
            })}
        </div>
    )
}

const PunksOfTheDayText = (props) => {
    if (!props.isVisible) {
        return (<></>)
    }

    return (
        <div className="punk-of-the-day-text">
            { "Punks-of-the-day".split('').map((letter, index) => {
                return <span key={index} className={"letter--" + index}>{letter.replace('-', ' ')}</span>
            })}
        </div>
    )
}

const PunksOfTheDayImages = (props) => {
    const { punks, slope, imgSize} = props;

    if (!props.isVisible) {
        return (<></>)
    }

    return punks.map((punk, index) => {
        const metadata = punk.metadata;
        const punkLink = `/punk/${punks[index].tokenId}`;
        const imageLink = `https://acy9375pq6.execute-api.us-east-1.amazonaws.com/shitPunkImage?index=${punks[index].metadata.id}`;
        const rarityLink = `/images/ranks/${getRarityImage(metadata.rank)}.png`
        return (
        
            <g key={punk.tokenId} className="punk-image-wrapper-outer">
                <g className={`punk-image-wrapper punk-image-wrapper--${index+1}`}>
                    <a href={punkLink}>
                        <image 
                            width={imgSize} 
                            height={imgSize} 
                            x={imgSize*index} 
                            y={slope*(7-index)} 
                            fill="none" 
                            className={`punk-row-image2 punk-row-image2--${index+1}`}
                            href={imageLink} 
                        />
                    </a>
                    <rect className="punk-image-overlay" x={imgSize*index}  y="0" width={imgSize} height={imgSize + slope*8} fill="white"/>
                    
                </g>
                <image className="punk-image-rank" x={imgSize*index}  y={imgSize*3/7} width={imgSize/2} href={rarityLink}></image>
            </g>
        )
    })
}

export default function PunksRow(props) {
    const midnight = new Date();
    midnight.setHours(0,0,0,0);
    const seed = midnight.getTime();

    const [punks, setPunks] = useState<any[]>([])
    const [isVisible, setVisible] = useState(false)
    const [hasCachedImages, setHashCachedImages] = useState(false)
    
    useEffect(() => {
        const fetchTokens = async () => {
            const random = new Random(
                MersenneTwister19937.seed(midnight.getTime())
            );
            
            const range = [...Array(7).keys()];
            const tokenIds = range.map(() => {
                const tokenId = random.integer(0, 9999);
                return tokenId;
            })

            //const random2 = new Random();
            //random2.shuffle(tokenIds);
    
            const metaDataBlob = await fetch(`https://acy9375pq6.execute-api.us-east-1.amazonaws.com/shitPunks2?indices=${tokenIds.join(',')}`)
            const metaDataJson: any[] = await metaDataBlob.json();
            if (metaDataJson && metaDataJson.length > 0) {
                const withTokenId = metaDataJson.map((metadata, index) => {
                    return {
                        metadata: {
                            ...metadata,
                        },
                        tokenId: tokenIds[index]
                    }
                })
    
                await Promise.all(metaDataJson.map(metadata => {
                    return new Promise((resolve, reject) => {
                        const img = new Image();
                        img.onload = resolve;
                        img.onerror = reject
                        img.src = metadata.image;
                    })
                }))
    
                setPunks(withTokenId)
            }
            
        }

        fetchTokens();
    }, [seed])
    
    const imgSize = 100/7;
    const slope = 5/7;

    const onChange = () => {
        console.log("Punk row visible!")
    }

    if (punks.length == 0) {
        return (<section className="section-punks-row section-punks-row--loading">
            <Spinner></Spinner>
        </section>)
    }

    return (

        <>
        <VisibilitySensor partialVisibility={true} onChange={(ev) => {
            //if (ev == true) {
                setVisible(ev)
            //}
        }}>    
            <section className={"section-punks-row" + (!isVisible ? " section-punks-row--hidden" : "")}>
            <div className="punk-row-2">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox={`0 0 100 ${imgSize + slope*7}`} className="punk-row-clipped">
                <defs>
                    <clipPath id="maskImage" clipPathUnits="userSpaceOnUse">
                        <polygon points={`0,${slope*8} 100,${slope * 1} 100,${imgSize} 0,${imgSize + slope*7}`} />
                    </clipPath>
                    <linearGradient id="Gradient3"  gradientTransform="rotate(75)">
                        <stop offset="0%" stopColor="#e53800"/>
                        <stop offset="50%" stopColor="#e53800"/>
                        <stop offset="70%" stopColor="#fbb41d"/>
                        <stop offset="100%" stopColor="#fbb41d"/>
                    </linearGradient>
                </defs>

                <g clipPath="url(#maskImage)">                  
                    { isVisible ? (
                        <rect className="punk-row-gradient-background" x="0" y="0" width="100" height={imgSize + slope*8} fill="url(#Gradient3)"/>
                    ) : null}
                    <PunksOfTheDayImages punks={punks} imgSize={imgSize} slope={slope} isVisible={isVisible}></PunksOfTheDayImages>                
                </g>
                </svg>

                <SpeechBubbles isVisible={isVisible}></SpeechBubbles>
                <PunksOfTheDayText isVisible={isVisible}></PunksOfTheDayText>                        
            </div>
            </section>
        </VisibilitySensor>

        </>
        
    )
}
