import Popup from 'reactjs-popup'
import { Web3Context } from "../../contexts/Web3Context";
import { useContext, useEffect, useState } from "react";
import { TransactionConfig } from 'web3-core'
import Spinner from '../widgets/Spinner';
import useAddressUtils from '../../hooks/AddressUtils';
import Button from '../widgets/Button';

export function ClaimRewardsButton(props: any) {
    const { web3, signedInAddress, contracts } = useContext(Web3Context)
    const [ balance, setBalance ] = useState<string | null>(null);
    const [ status, setStatus ] = useState<any>({});
    const { txLink } = useAddressUtils();

    useEffect(() => {
        const fetchRewards = async () => {
            if (!web3) {
                return null;
            }

            const contractJson = contracts[props.contract]
            const contract = new web3.eth.Contract(contractJson.abi, contractJson.address)
            const reflectionBalances = await contract.methods.getReflectionBalances().call({from: signedInAddress})
            const bnb = web3.utils.fromWei(reflectionBalances, 'ether')
            setBalance(bnb);
        }

        setBalance(null);
        fetchRewards();
    }, [web3, signedInAddress, contracts])

    const claimRewards = async () => {
        if (!web3) {
            return;
        }

        setStatus({
            inProgress: true
        })

        const contractJson = contracts[props.contract];

        try {
            const contract = new web3.eth.Contract(contractJson.abi, contractJson.address)
            const tx = await contract.methods.claimRewards();
            const data = tx.encodeABI();
            const gas = await tx.estimateGas({from: signedInAddress});
            const gasPrice = await web3.eth.getGasPrice();       
            const nonce = 0x00;
            const transcationConfig: TransactionConfig = {
                from: signedInAddress,
                to: contractJson.address,
                gas: gas,
                gasPrice: gasPrice,
                data: data,
                chainId: 0x38,
            }
    
            const receipt = await web3.eth.sendTransaction(transcationConfig);
            setStatus({
                receipt: receipt
            })
        } catch (e: any) {
            setStatus({
                error: e.message
            })
        }
    }


    if (status.inProgress) {
        return (
            <div className="reward-info">
                Claiming reward... <Spinner></Spinner>
            </div>    
        )
    } else if (status.receipt) {
        return (
            <div className="reward-info">
                Rewards have been claimed.
                <a href={txLink(status.receipt.transactionHash)} rel="noreferrer" target="_blank">View transaction</a>
            </div>    
        )
    } else {
        return (
            <div className="reward-info">
                <div>{props.title}</div>
                <div>{balance} BNB</div>
                <Button className="button" onClick={ claimRewards }
                >Claim</Button>

                { status.error ? <div className='claim-reward-error'>{status.error}</div> : null}
            </div>
        )
    }
}

export function ClaimRewardsModal(props: any) {
    const { web3, signedInAddress, contracts } = useContext(Web3Context)

    return (
        <Popup  
            modal
            nested
            trigger={
                ({...props.children})
            }> 
            {(close: any) => (<div className="modal claim-rewards-modal">
                
                <div className="modal">
                    <Button className="button--modal-close" onClick={close}>&times;</Button>
                    <h2>Rewards have been discontinued</h2>
                    <div className="content">
                    It has been discovered that there are legal issues with SEC classifications of securities on NFT projects that give reflections which would put our project in jeopardy, so ShitPunks has found a new way to get the rewards to our community. The rewards now sent to the ShitPunks DAO for community use there! Head over to our Discord server, which is where the DAO is located.
                    </div>
                    <div className="section-claim">
                        <ClaimRewardsButton contract="shitpunks" title="Token rewards"/>
                        <ClaimRewardsButton contract="shitmarket" title="Marketplace rewards"/>
                    </div>
                </div>
            </div>)
        }
    </Popup>)
}
