import VisibilitySensor from 'react-visibility-sensor'

export default function SocialLinks(props) {

    const iconSize = ""

    const links = [
        { icon: iconSize+'fab fa-discord', title: 'Discord', link: 'https://discord.gg/ShitPunks' },
        { icon: iconSize+'fab fa-twitter', title: 'Twitter', link: 'https://twitter.com/Shit_Punks' },
        { icon: iconSize+'fab fa-telegram', title: 'Telegram', link: 'https://t.me/ShitPunksOfficial' },
        { icon: iconSize+'fab fa-instagram', title: 'Instagram', link: 'https://instagram.com/Shit_Punks' },
        //{ icon: iconSize+'fab fa-medium', title: 'Medium', link: 'https://shitpunks.medium.com/' },
    ]

    return (
        <VisibilitySensor>
            {({isVisible}) =>
                <div className="social-icons">
                { links.map((item, index) => {
                    return (
                        <a rel="noreferrer" target="_blank" className={"social-icon" + (isVisible || true ? " social-icon--animate" : "")} style={{animationDelay: 0.2+(index*0.1) + 's'}} key={item.title} href={item.link}><i className={item.icon}></i></a>
                    )
                })}
                </div>
            }
        </VisibilitySensor>
    )
    
}