import { NavLink } from "react-router-dom";
import { useLocation } from 'react-router-dom'

export default function MainMenu() {
    const location = useLocation();

    const links = [
        { to: '/home', text: 'home' },
        { to: '/marketplace/forsale', text: 'marketplace', activeMatch: "/marketplace" },
        { to: '/collection', text: 'collection' },
        { to: '/nftdrops/upcoming', text: 'NFT drops', activeMatch: "/nftdrops" },
    ]

    const activeClassName = "main-menu-link main-menu-link--active"
    const inActiveClassName = "main-menu-link"

    return (
        <div className="main-menu">
            { links.map(link => {
                const active = location.pathname.startsWith(link.activeMatch || link.to)
                return (
                    <NavLink key={link.to} to={link.to} className={({ isActive }) => (isActive || active) ? activeClassName : inActiveClassName }>{link.text}</NavLink>
                )
            }) }           
        </div>
    )
}