import React from "react";
import { useState, useMemo } from 'react'
import Web3 from "web3";
import { TransactionConfig } from 'web3-core'
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";

/*
import contract_pcs from "../data/contract_pcs.json"
import contract_shitpunks from "../data/contract_shitpunks.json"
import contract_shitpunks2 from "../data/contract_shitpunks2.json"
import contract_shitmarket from "../data/contract_shitmarket.json"
*/

const CHAIN_ID = 56;
const RPC_URL = {
    56: 'https://bsc-dataseed.binance.org/',
    97: 'https://data-seed-prebsc-1-s1.binance.org:8545/'
} 

interface Contracts {
    fetched: boolean;
    pcs: any;
    shitpunks: any;
    shitpunks2: any;
    shitmarket: any;
}

interface Web3Data {
    web3: Web3 | undefined,
    signedInAddress: string;
    signedInAddressShort: string;
    connect: any;
    disconnect: any;
    contracts: Contracts;
    chain: number;
    writeContract: any;
}

const defaultWeb3Data: Web3Data = { 
    web3: undefined, 
    signedInAddress: "", 
    signedInAddressShort: "",
    connect: null, 
    disconnect: null,
    contracts: { fetched: false, pcs: undefined, shitpunks: undefined, shitpunks2: undefined, shitmarket: undefined },
    chain: -1,
    writeContract: undefined
}

export const Web3Context = React.createContext<Web3Data>(defaultWeb3Data)

export const Web3Provider: React.FC = ({
    ...props
}) => {
    console.log("Web3Provider")

    const [web3, setWeb3] = useState<Web3>();
    const [signedInAddress, setSignedInAddress] = useState("");
    const [contracts, setContracts] = useState<any>({})
    const [chain, setChain] = useState<number>(-1)

    const defaultWeb3 = new Web3(RPC_URL[CHAIN_ID])

    useMemo(async () => {
        console.log("Fetching contracts")

        const fetchContract = async (url) => {
            try {
                const res = await fetch(url)
                const json = await res.json();
                return json;
            } catch (e) {
                console.log("error fetching", url, e);
            }
        }

        const [
            contract_pcs,
            contract_shitpunks,
            contract_shitpunks2,
            contract_shitmarket
        ] = await Promise.all([
            fetchContract("/data/pcs.json"),
            fetchContract("/data/shitpunks.json"),
            fetchContract("/data/shitpunks2.json"),
            fetchContract("/data/shitpunksmarket.json")
        ]);

        console.log("Fetching contracts complete")

        setContracts({
            fetched: true,
            pcs: contract_pcs,
            shitpunks: contract_shitpunks,
            shitpunks2: contract_shitpunks2,
            shitmarket: contract_shitmarket
        })
    }, [])

    const web3Modal = useMemo(() => {
        console.log("creating web3 modal")
        const providerOptions = {
            walletconnect: {
                package: WalletConnectProvider,
                options: {
                    rpc: RPC_URL,
                    network: 'binance',
                    chainId: CHAIN_ID,
                    //infuraId: process.env.REACT_APP_INFURA_ID,
                },
            },
        };

        return new Web3Modal({
            cacheProvider: true,
            providerOptions: providerOptions,
            theme: "dark"
        });       
    }, [])

    const connect = async () => {
        console.log("connect was called!");
        
        if (!web3Modal) {
            return;
        }

        console.log("cache", web3Modal.cachedProvider)
        
        let provider = await web3Modal.connect();  
        let web3 = new Web3(provider);
        
        // Subscribe to chainId change
        provider.on("chainChanged", (chainId: number) => {
            if (chainId != CHAIN_ID) {
                disconnect();
            }
            console.log("chainChanged", chainId);
        });

        const chainId = await web3.eth.getChainId();
        if (chainId != CHAIN_ID) {
            //alert("Wrong chain!")
            await (web3.currentProvider as any)?.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: web3.utils.numberToHex(CHAIN_ID) }], // chainId must be in hexadecimal numbers
            });

            const chainId = await web3.eth.getChainId();
            //if (chainId == CHAIN_ID) {
                setImmediate(() => {
                    console.log("delaying connect...")
                    connect();
                })
            //}
            return;
        }

        console.log("Connected to chain: " + chainId)
        setWeb3(web3)
        setChain(chainId);

        if (chainId == CHAIN_ID) {
            const address = provider.selectedAddress || provider.accounts[0]
            setSignedInAddress(address)
            return address;
        } else {
            setSignedInAddress("")
            return "";
        }
    }

    const disconnect = async () => {
        await web3Modal?.clearCachedProvider();
        setSignedInAddress("")
    }

    useMemo(async () => {
        console.log("useMemo - calling connect()")
        if (web3Modal.cachedProvider) {
            await connect();
        }
    }, [])

    const getDefaultWeb3 = () => {
        console.log("using default web3")
        return defaultWeb3;
    }

    const writeContract = async (contractJson, method, methodArgs, value) => {
        if (!web3) {
            return {
                success: false,
                error: "Not connected"
            }
        }
        try {
            const contract = new web3.eth.Contract(contractJson.abi, contractJson.address)
            const tx = await contract.methods[method](...methodArgs);
            const data = tx.encodeABI();
            const gas = await tx.estimateGas({from: signedInAddress, value: value});
            const gasPrice = await web3.eth.getGasPrice();       
            const transcationConfig: TransactionConfig = {
                from: signedInAddress,
                to: contractJson.address,
                gas: gas,
                value: value,
                gasPrice: gasPrice,
                data: data,
                chainId: CHAIN_ID,
            }
        
            const receipt = await web3.eth.sendTransaction(transcationConfig);    
            return {
                success: true,
                receipt: receipt
            }
        } catch (e: any) {
            console.log(e);
            return {
                success: false,
                error: e.message || e
            }
        }   
    }

    return (<Web3Context.Provider value={{
        web3: web3 || getDefaultWeb3(),
        signedInAddress: signedInAddress,
        signedInAddressShort: signedInAddress.length > 0 ? (signedInAddress.substr(0,6) + "..." + signedInAddress.substr(signedInAddress.length - 3, 3)) : "",
        connect: connect,
        disconnect: disconnect,
        contracts: contracts,
        chain: chain,
        writeContract: writeContract
    }}>{props.children}</Web3Context.Provider>)
}
