import Popup from "reactjs-popup";
import { useContext, useState } from "react";
import { Web3Context } from '../contexts/Web3Context';
import useAddressUtils from "../hooks/AddressUtils";
import Input from "../components/widgets/Input";
import Spinner from "../components/widgets/Spinner";
import Button from "../components/widgets/Button";

export default function AdjustPriceModal(props) {

    const [price, setPrice] = useState(props.price)
    const [result, setResult] = useState<any>()
    const {web3, writeContract, contracts} = useContext(Web3Context)
    const { txLink } = useAddressUtils()

    const adjustPrice = async () => {
        if (!web3) {
            setResult({
                success: false,
                error: "Wallet is not connected"
            })
            return;
        }
        setResult({
            success: false,
            waiting: true
        })
        const priceInWei = web3.utils.toWei(price, 'ether')

        const adjustPriceOnMarketplace = async () => {
            if (props.sellInfo.marketplace == 'PancakeSwap') {
                return await writeContract(contracts.pcs, "modifyAskOrder", [contracts.shitpunks2.address, props.tokenId, priceInWei])
            } else {
                return await writeContract(contracts.shitmarket, "createListing", [props.tokenId, priceInWei])
            }
        }

        const result = await adjustPriceOnMarketplace();
        
        setResult(result);
    }

    const render = (close: any) => {
        if (result && result.success) {
            return (<>
                <div>{props.metadata.name} has been listed for {price} BNB.</div>
                <div>View <a rel="noreferrer" target="_blank" href={txLink(result.receipt.transactionHash)}>transaction here</a>.</div>
                <Button onClick={close} className="button button button-ok">Ok</Button>
            </>)
        } else if (result && result.waiting) {
            return <div><Spinner></Spinner></div>
        } else {
            return (
                <>
                <div className="price-input-container">
                    <Input className="price-input" type="number" label="BNB" value={price} onChange={setPrice}></Input>
                    <Button onClick={adjustPrice} className="button">Adjust price</Button>
                </div>
                { (result && result.error) ? <div className="error-text">{result.error}</div> : null}
                </>
            )
        }
    }

    return (
        <Popup
            nested
            modal
            onClose={() => {
                props.onClose()
            }}
            trigger={
                ({...props.children})
            }>
            {(close: any) => (<div className="modal adjust-price-modal">   
                    <Button className="button--modal-close" onClick={close}>&times;</Button>
                    <h3>Adjusting price of {props.metadata.name}</h3>
                    <div className="content">
                        {render(close)}
                    </div>
            </div>)
            }
        </Popup>
    )
}