import Select from 'react-select'

const colourStyles = {
    control: (styles: any) => ({ 
        ...styles, 
        backgroundColor: 'transparent',
        border: 'none !important',
        boxShadow: 'none',
        color: 'white',
        paddingLeft: "1rem",
        cursor: "pointer",
    }),
    option: (styles: any, {}) => {
        return {
            ...styles,
            backgroundColor: 'white',
            color: 'black'
        }
    },
    singleValue: (styles: any, {}) => ({
        ...styles,
        color: 'rgb(255, 122, 0)'
    }),
    placeholder: (styles: any, {}) => ({
        ...styles,
        margin: 0,
        color: "white",
        fontFamily: "Poppins, sans-serif"
    }),
    valueContainer: (styles: any, {}) => ({
        ...styles,
        padding: 0
    }),
    dropdownIndicator: (styles: any, {}) => ({
        ...styles,
        color: '#666',
        paddingLeft: '1.5rem'
    })    
}

export default function SelectField(props) {
 
    const CustomOption = (optionProps: any) => {
        const countDiv = optionProps.data.count != undefined ? (<div className='count'>{(optionProps.data.count / 100).toFixed(2)}%</div>) : (<div></div>)
        const floorDiv = optionProps.data.floor != undefined ? (<div className='floor'>
            <img className="bnb-icon" src="/images/bnb_icon.png" />{optionProps.data.floor}
        </div>) : (<div></div>)
        return (
            <div className="select-option" {...optionProps.innerProps}>
                <div className='label'>{optionProps.data.label}</div>
                {props.showFloorPrices ? floorDiv : null}
                {!props.showFloorPrices ? countDiv : null}
            </div>
        )
    }

    const handleChange = (event: any) => {
        props.onChange({
            type: props.name,
            value: event ? event.value : null
        })
    }

    return (
        <Select 
            className="select-control"
            classNamePrefix="select-control"
            name={props.name}
            isClearable={props.isClearable}
            components={{
                IndicatorSeparator: () => null,
                Option: CustomOption
            }}
            placeholder={props.placeholder}
            options={props.options} 
            styles={colourStyles}
            //defaultValue={this.getOptionByValue(this.state.selected)}
            defaultValue={props.defaultValue}
            onChange={handleChange}
        />
    )
}