import PunkCard from "./PunkCard";
import Spinner from "../widgets/Spinner";

export default function PunkCardList(props) {
    const className = `punk-card-list punk-card-list-${props.classSuffix}`
    if (props.loading) {
        return (
            <div className={`${className} punk-card-list--loading`}>
                <Spinner></Spinner>
            </div>
        )
    } 
    
    return (
        <>
            <div className="num-results">{props.total} Results</div>
            <div className={`${className}`}>
                {props.tokens.map(token => (<PunkCard key={token.metadata.tokenId} {...token}></PunkCard>))}
            </div>
        </>
    )
}