import { Routes, Route } from 'react-router-dom';
import Collection from '../Collection';
import Home from '../Home';
import Marketplace from '../marketplace/Marketplace';
import { NFTCalendar } from '../NFTCalendar';
import NFTCalendarForm from '../NFTCalendarForm'
import PunkDetails from '../PunkDetails';

export default function Main() {
    return (
        <div className="main">
            <Routes>
                <Route path="/punk/:tokenId" element={<PunkDetails/>}></Route>
                <Route path="/token/:tokenId" element={<PunkDetails/>}></Route>
                <Route path="/shitpunk/:punkId" element={<PunkDetails/>}></Route>
                <Route path="/marketplace/activity" element={<Marketplace key="activity" marketplaceMode="activity"/>}></Route>
                <Route path="/marketplace/all" element={<Marketplace key="all" marketplaceMode="all"/>}></Route>
                <Route path="/marketplace/*" element={<Marketplace key="forsale" marketplaceMode="forsale"/>}></Route>
                <Route path="/collection/*" element={<Collection/>}></Route>
                <Route path="/nftdrops/released" element={<NFTCalendar key="released" mode="released"/>}></Route>
                <Route path="/nftdrops/form" element={<NFTCalendarForm/>}></Route>
                <Route path="/nftdrops/*" element={<NFTCalendar key="upcoming" mode="upcoming"/>}></Route>
                
                <Route path="*" element={<Home/>}></Route>
            </Routes>
        </div>
    )
}