import { NavLink } from "react-router-dom";

export default function PageMenuBar(props) {
    const activeClassName = "page-menu-bar-button page-menu-bar-button--active"
    const inActiveClassName = "page-menu-bar-button"

    const links = props.links || [
        {
            title: "For sale",
            icon: "fas fa-list",
            link: "/marketplace/forsale"
        },
        {
            title: "Trades",
            icon: "fas fa-wave-square",
            link: "/marketplace/activity"
        },
        {
            title: "All tokens",
            icon: "fas fa-star",
            link: "/marketplace/all"
        }
    ]

    return (
        <div className="page-menu-bar">
            { links.map(item => <NavLink to={item.link} className={({ isActive }) => isActive ? activeClassName : inActiveClassName }><i className={item.icon}></i> {item.title}</NavLink>)}
        </div>
    )
}
