import { useContext, useEffect } from "react";
import Input from "./widgets/Input";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import { Web3Context } from "../contexts/Web3Context";
import PunkCardList from "./punk-card/PunkCardList";
import PunkSearchBar from "./PunkSearchBar";
import { DiamondHands } from "./DiamondHands";

const fetchFromPancake = async (web3, signedInAddress, contract_pcs) => {
    if (!web3) {
        return [];
    }
    const contract = new web3.eth.Contract((contract_pcs as any).abi, contract_pcs.address)
    const asks = await contract.methods.viewAsksByCollectionAndSeller("0x11304895f41C5A9b7fBFb0C4B011A92f1020EF96", signedInAddress, 0, 500).call()
    return asks.tokenIds.map((tokenId, index) => {
        return {
            seller: asks.askInfo[index].seller,
            price: web3.utils.fromWei(asks.askInfo[index].price, 'ether'),
            tokenId: tokenId,
            marketplace: "pcs"
        }
    });
}

const fetchFromToken = async (web3, signedInAddress, contractJson: any) => {
    if (!web3) {
        return [];
    }
   
    try {
        const contract = new web3.eth.Contract(contractJson.abi, contractJson.address)
        const balance = parseInt(await contract.methods.balanceOf(signedInAddress).call())
        if (balance == 0) {
            return []
        }
  
        const range = [...Array(balance).keys()];
        let assetIds = await Promise.all(range.map(async i => {
            const assetId = await contract.methods.tokenOfOwnerByIndex(signedInAddress, i).call()
            return {
                tokenId: assetId
            }
        }))

        return assetIds;
    } catch (e) {
        console.error(e);
        return [];
    }
  }


  const sortTokens = (searchParams, tokens) => {
    const secondarySort = (firstSortValue, a, b) => {
        if (firstSortValue == 0) {
            return a.metadata.rank - b.metadata.rank;
        } else {
            return firstSortValue;
        }
    }
    
    const sortMode = searchParams.get('sort')
    return tokens.sort((a, b) => {
        if (sortMode == 'price-low') {
            const priceA = parseFloat(a.price) || Number.MAX_VALUE;
            const priceB = parseFloat(b.price) || Number.MAX_VALUE;
            return secondarySort(priceA - priceB, a, b);
        } else if (sortMode == 'price-high') {
            const priceA = parseFloat(a.price) || 0;
            const priceB = parseFloat(b.price) || 0;
            return secondarySort(priceB - priceA, a, b);
        } else if (sortMode == 'attributeCountLow') {
            return secondarySort(a.metadata.attributes.length - b.metadata.attributes.length, a, b);
        } else if (sortMode == 'attributeCountHigh') {
            return secondarySort(-(a.metadata.attributes.length - b.metadata.attributes.length), a, b);
        } else if (sortMode == 'rank-low') {
            return secondarySort(b.metadata.rank - a.metadata.rank, a, b);
        } else if (sortMode == 'rank-high') {
            return secondarySort(a.metadata.rank - b.metadata.rank, a, b);
        }
        return secondarySort(0, a, b);
    });
}

export default function Collection() {
    const [walletId, setWalletId] = useState("")
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState<any>(null)
    const [invalidAddress, setInvalidAddress] = useState(false)
    const [inventory, setInventory] = useState<any>({tokens: []});
    const {web3, signedInAddress, contracts} = useContext(Web3Context)
    const [searchParams, setSearchParams] = useSearchParams();
    const [refreshSalt, setRefreshSalt] = useState(0);

    useEffect(() => {
        if (searchParams.has('wallet')) {
            const address = searchParams.get('wallet')
            setWalletId(address || "");
        } else {
            setWalletId(signedInAddress)
        }
    }, [searchParams, signedInAddress])

    useEffect(() => {
        if (!contracts.fetched) {
            return;
        }

        const fetchTokens = async () => {
            console.log("fetching tokens", walletId, web3);
            setError(null);

            setLoading(true);
            setInventory({tokens: []});

            if (web3 && web3.utils.isAddress(walletId)) {
                setInvalidAddress(false);

                const res = await Promise.all([
                    fetchFromPancake(web3, walletId, contracts.pcs),
                    fetchFromToken(web3, walletId, contracts.shitpunks),
                    fetchFromToken(web3, walletId, contracts.shitpunks2),
                ]).catch(err => {
                    console.log("fetch error", err);
                })

                if (!res) {
                    setError("Network error");
                    setLoading(false);
                    return;
                }

                const notOnPcsIds = [...res[1], ...res[2]]
                const contractShitMarket = new web3.eth.Contract((contracts.shitmarket as any).abi, contracts.shitmarket.address)
                const notOnPcs = await Promise.all(notOnPcsIds.map(async tokenInfo => {
                    const listing = await contractShitMarket.methods.getListing(tokenInfo.tokenId).call()
                    const forSale = listing[4] && listing[0].toLowerCase() == walletId.toLowerCase();
                    const listingInfo = forSale ? {
                        seller: listing[0],
                        buyer: listing[1],
                        price: web3.utils.fromWei(listing[3], 'ether'),
                        marketplace: "shitmarket"
                    } : {}
                    return {
                        tokenId: tokenInfo.tokenId,
                        ...listingInfo
                    }
                }))
                
                const tokenList = [...res[0], ...notOnPcs]
                if (tokenList.length == 0) {
                    setInventory({
                        tokens: []
                    })
                    setLoading(false);
                    return;
                }


                const balances = await Promise.all(tokenList.map(async token => {
                    const tokenId = token.tokenId;
                    const bal = await contractShitMarket.methods.getReflectionBalance(tokenId).call()
                    return parseFloat(web3.utils.fromWei(bal, 'ether'));
                })).catch(err => {
                })

                if (!balances) {
                    setError("Network error");
                    setLoading(false);
                    return;
                }

                const totalBal = balances.reduce((acc, cur) => acc +cur, 0)
                console.log("total", totalBal);
                

                const tokenIdsString = tokenList.map(token => token.tokenId).join(',')
                try {
                    const metaDataBlob = await fetch(`https://acy9375pq6.execute-api.us-east-1.amazonaws.com/shitPunks2?indices=${tokenIdsString}`)
                    const metaDataJson: any[] = await metaDataBlob.json();
                    const withTokenId = metaDataJson.map((metadata, index) => {
                        return {
                            metadata: {
                                ...metadata,
                                tokenId: tokenList[index].tokenId
                            },
                            ...tokenList[index]
                        }
                    }).sort((a,b) => a.metadata.rank - b.metadata.rank);
                    setInventory({tokens: sortTokens(searchParams, withTokenId)});
                    setLoading(false);
                } catch (e) {
                    setLoading(false);
                    setError("Network error");
                }
                

            } else {
                setLoading(false);
                setInvalidAddress(true);
            }
            
        }
        fetchTokens();
    }, [contracts.fetched, walletId, contracts, web3, refreshSalt])

    useEffect(() => {
        setInventory({tokens: sortTokens(searchParams, inventory.tokens)});
    }, [searchParams])

    const sortOptions = [
        {
            label: 'Rank: Low to high',
            value: 'rank-low'
        },
        {
            label: 'Rank: High to low',
            value: 'rank-high'
        },
        {
            label: 'Attribute count: Low to high',
            value: 'attributeCountLow'
        },
        {
            label: 'Attribute count: High to low',
            value: 'attributeCountHigh'
        },
        {
            label: 'Price: Low to high',
            value: 'price-low'
        },
        {
            label: 'Price: High to low',
            value: 'price-high'
        }
    ]

    const walletInputChanged = (address) => {
        setSearchParams({
            wallet: address
        }, {
            replace: true
        })
    }

    return (<div className="page page-collection">
        <h1>Collection<span>.</span></h1>

        { signedInAddress.length > 0 && signedInAddress === walletId ? (
            <DiamondHands></DiamondHands>
        ) : null}
        
        <div className="collection-input">
            <Input label={"Wallet address"} value={walletId}
                onChange={walletInputChanged}
            ></Input>

            { invalidAddress ? (
                <div>Invalid address</div>
            ) : null }
        </div>

        { invalidAddress ? null : (
            <>
            <PunkSearchBar 
                sortOptions={sortOptions}
                defaultSort={sortOptions[1]}
                refresh={() => { setRefreshSalt(refreshSalt + 1)}}
            />
            <PunkCardList total={inventory.tokens.length} classSuffix={"collection"} tokens={inventory.tokens} loading={loading}></PunkCardList>
            </>
        )}

        { error ? (
            <div className="network-error">Network error :( Please try again later.</div>
        ) : null}
    </div>)
}
