import MainMenu from "../topbar/MainMenu";
import Wallet from "../topbar/Wallet";

export default function TopBar() {
    return (
        <div className="top-bar">
            <div id="shitpunks-brand">
                <img id="shitpunks-logo" src="/images/logo.png"/>
                <div id="shitpunks-title"><a href="/home">ShitPunks<span>.</span></a></div>
            </div>
            <MainMenu></MainMenu>
            <div id="wallet">
                <Wallet></Wallet>
            </div>
        </div>
    )
}

