import {Img} from 'react-image'
import Spinner from '../widgets/Spinner';
import { useLocation } from 'react-router-dom'
import { useState, useContext } from 'react';
import { Web3Context } from "../../contexts/Web3Context"
import useAddressUtils from '../../hooks/AddressUtils';
import { useSearchParams } from "react-router-dom";

import TimeAgo from 'javascript-time-ago'
import en from 'javascript-time-ago/locale/en.json'
TimeAgo.addLocale(en)
const timeAgo = new TimeAgo('en-US')

const getRarityImage = (rank) => {
    if (rank < 100) {
        return "legendary"
    } else if (rank < 1000) {
        return "epic"
    } else if (rank < 2500) {
        return "rare"
    } else if (rank < 5000) {
        return "uncommon"
    } else {
        return "common"
    } 
}

function UpdatedText(props) {

    if (props.event == 'AskUpdate') {
        if (props.prevListing > parseFloat(props.price)) {
            return (<>
                <span className="reduced"><i className="fas fa-arrow-circle-down"></i></span> from {props.prevListing} BNB
            </>)
        } else {
            return (<>
                <span className="increased"><i className="fas fa-arrow-circle-up"></i></span> from {props.prevListing} BNB
            </>)
        }
    } else if (props.event == 'AskNew' || props.event == 'ListingCreated') {
        return "Listed "
    } if (props.event == 'Trade' || props.event == 'Sale') {
        return "Sold "
    } else {
        return "Updated ";
    }
    
}

export default function PunkCard(props) {
    const {web3, contracts} = useContext(Web3Context)
    const [showFront, setShowFront] = useState(true);
    const { formatDate, formatAddress, formatAddressMini, addressLink, walletLink } = useAddressUtils();
    const [searchParams, setSearchParams] = useSearchParams();


    const metadata = props.metadata;
    const rarityImage = '/images/ranks/' + getRarityImage(metadata.rank) +'.png';
    const punkImage = metadata.image; // 
    //const punkImage = 'http://www.deelay.me/300000/http://placehold.it/500x500'

    const location = useLocation();

    const updatedAt = props.timestamp ? timeAgo.format(props.timestamp) : null;


    const marketplaceText = () => {
        if (props.marketplace == 'pcs') {
            return "PancakeSwap";
        } else {
            return "ShitMarket"
        }
    }

    const marketplaceImage = (() => {
        switch (props.marketplace) {
            case "shitmarket":
                return "/images/poop-black.png"
            case "pcs":
                return "/images/pcs_icon.png"
            default:
                return null;
        }
    })();

    const flipCard = () => {
        setShowFront(!showFront);
    }

    return (
        <div className="punk-card">
            <div className={"punk-card-side punk-card-back" + (showFront ? "" : " punk-card-back--visible")}>

                <div className="image-and-seller">
                    <div className="punk-image-container">
                    <Img className="punk-image" 
                        src={punkImage}
                        loader={<Spinner/>}
                        container={(children) => {
                            return (
                                <div className='punk-image-wrapper'>
                                    {children}
                                </div>
                            )
                        }}
                    />    
                    </div>

                    <div>
                        { (props.seller 
                            && props.seller != '0x'
                            && !props.seller.startsWith('0x00000000')
                        ) ? (
                            <>
                                <div className="punk-card-back-item-label">Seller</div>
                                <div className="punk-card-back-item-value">
                                    {walletLink(props.seller)}
                                    <a href={addressLink(props.seller)}>{formatAddressMini(props.seller)}</a>
                                </div>
                            </>
                        ) : null }

                        { (props.buyer 
                            && props.buyer != '0x'
                            && !props.buyer.startsWith('0x00000000')
                        ) ? (
                            <>
                                <div className="punk-card-back-item-label">Buyer</div>
                                <div className="punk-card-back-item-value">
                                    {walletLink(props.buyer)}
                                    <a href={addressLink(props.buyer)}>{formatAddressMini(props.buyer)}</a>
                                </div>
                            </>
                        ) : null }
               
                 
                    </div>

                    
                </div>
                
                <div className="punk-card-back-item-label">Attributes</div>
                <div className="punk-card-back-attribute-list">
                    {metadata.attributes.map(attr => {
                        const isSelected = searchParams.get(attr.trait_type) === attr.value;
                        return (<div key={attr.trait_type} onClick={() => {
                            const params = searchParams;
                            if (isSelected) {
                                params.delete(attr.trait_type);
                            } else {
                                params.set(attr.trait_type, attr.value)
                            }
                            
                            setSearchParams(params, { replace: false });
                        }}
                         className={"punk-card-back-attribute-item" + (isSelected ? " punk-card-back-attribute-item--selected" : "")}>
                            <div className="punk-card-back-trait-type">{attr.trait_type}</div>
                            <div className="punk-card-back-trait-value">{attr.value}</div>
                        </div>)
                    })}
                </div>
                
                
                <div className="dogear" onClick={flipCard}>
                    <div className="triangle-bottomright triangle-bottomright--back"></div> 
                </div>
            </div>
            <div className={"punk-card-side punk-card-front" + (showFront ? " punk-card-front--visible" : "")}>
            <a href={"/token/"+props.metadata.tokenId}>
            <div className="punk-image-container">
                <Img className="punk-image" 
                    src={punkImage}
                    loader={<Spinner/>}
                    container={(children) => {
                        return (
                            <div className='punk-image-wrapper'>
                                {children}
                            </div>
                        )
                    }}
                />             
                <img className='punk-card-rarity' src={rarityImage} alt='Rarity'></img>
                
                { (metadata.attributes.length <= 2 || metadata.attributes.length >= 6) ? (
                    <div className='punk-card-attrcount'>{metadata.attributes.length-1} attr</div>
                ) : null }
                {(marketplaceImage && marketplaceImage.length > 0) ? (<img className="punk-card-marketplace-image" src={marketplaceImage}></img>) : null}
            </div>
            </a>
            
              
             <div className="punk-details">
                <div className="punk-name">
                    {metadata.name}
                </div>

{
    /*
      <ConnectWrapper connectText="Connect wallet to buy">
                    <BuyPunkModal 
                        onClose={() => {
                            //setTradeHistorySalt(tradehistorySalt + 1);
                            //fetchOwner()
                        }}
                        metadata={metadata} 
                        tokenId={metadata.tokenId}
                        price={props.price}
                        sellInfo={{
                            marketplace: props.marketplace
                        }}
                    >
                        <Button className="button">Buy</Button>
                    </BuyPunkModal>
                </ConnectWrapper>
    */
}
              

                <div className="punk-statrow">
                    <div className="punk-stat">
                        <div className="punk-stat-title">Token&nbsp;id</div>
                        <div className="punk-stat-value">{metadata.tokenId}</div>
                    </div>
                    <div className="punk-stat">
                        <div className="punk-stat-title">Rank</div>
                        <div className="punk-stat-value">{metadata.rank}</div>
                    </div>
                    <div className="punk-stat">
                        <div className="punk-stat-title">Price</div>
                        <div className="punk-stat-value">
                            {props.price ? (
                                <>
                                    <img className="bnb-icon" src="/images/bnb_icon.png" />{props.price}
                                </>
                            ) : (<></>) }
                            
                        </div>
                    </div>
                </div>


                { updatedAt ? (
                    <div className="punk-timestamp">
                        { UpdatedText(props) }<br/>{updatedAt}
                    </div>
                ) : null}               
             </div>

                    <div className="dogear" onClick={flipCard}>
                        <div className="triangle-bottomright"></div> 
                    </div>
                
            </div>

            
 
        </div>
    )
}