export default function Input(props: any) {
    const onChange = (ev) => {
        if (props.onChange) {
            props.onChange(ev.target.value);
        }
    }

    const className = "input-container " + (props.className || "")

    if (props.type == 'number') {
        return (<div className={className}>
            <input 
                placeholder="&nbsp;"
                className="input" 
                onChange={onChange}
                value={props.value}
                type='number'
                pattern="[0-9]*" 
                inputMode="decimal"
            
            ></input>    
            <div className="placeholder">{props.label}</div>
        </div>
    )
    } else {
        return (<div className={className}>
            <input 
                placeholder="&nbsp;"
                className="input" 
                onChange={onChange}
                value={props.value}
                type='text'
            ></input>    
            <div className="placeholder">{props.label}</div>
        </div>
    )
    }


}
