import Popup from "reactjs-popup";
import { useContext, useState } from "react";
import { Web3Context } from '../contexts/Web3Context';
import useAddressUtils from "../hooks/AddressUtils";
import Spinner from "../components/widgets/Spinner";
import Button from "../components/widgets/Button";

export default function BuyPunkModal(props) {

    const [price, setPrice] = useState(props.price)
    const [result, setResult] = useState<any>()
    const {web3, writeContract, contracts} = useContext(Web3Context)
    const { txLink } = useAddressUtils()

    const signTransaction = async () => {
        if (!web3) {
            setResult({
                success: false,
                error: "Wallet is not connected"
            })
            return;
        }

        setResult({
            success: false,
            waiting: true
        })
        
        const value = web3.utils.toWei(price, 'ether');

        console.log("props.sellInfo.marketplace", props.sellInfo.marketplace, props.tokenId, price);

        if (props.sellInfo.marketplace === 'PancakeSwap' ||
            props.sellInfo.marketplace === 'pcs') {
            const result = await writeContract(contracts.pcs, "buyTokenUsingBNB", [contracts.shitpunks2.address, props.tokenId], value)
            setResult(result);
        } else {
            const result = await writeContract(contracts.shitmarket, "buy", [props.tokenId], value)
            setResult(result);
        }        
    }

    const render = (close: any) => {
        if (result && result.success) {
            return (<>
                <div>{props.metadata.name} has been bought for {price} BNB.</div>
                <div>View <a rel="noreferrer" target="_blank" href={txLink(result.receipt.transactionHash)}>transaction here</a>.</div>
                <Button onClick={close} className="button button button-ok">Ok</Button>
            </>)
        } else if (result && result.waiting) {
            return <div><Spinner></Spinner></div>
        } else {
            return (
                <>
                <div>Asking price: {price} BNB</div>
        
                { (result && result.error) ? (
                    <>
                        <Button onClick={signTransaction} className="button">Try again</Button>
                        <div className="error-text">{result.error}</div>
                    </>
                ) : null}
                </>
            )
        }
    }

    return (
        <Popup
            nested
            modal
            onOpen={signTransaction}
            onClose={() => {
                props.onClose()
            }}
            trigger={
                ({...props.children})
            }>
            {(close: any) => (<div className="modal buy-punk-modal">   
                    <Button className="button--modal-close" onClick={close}>&times;</Button>
                    <h3>Buying {props.metadata.name}</h3>
                    <div className="content">
                        {render(close)}
                    </div>
            </div>)
            }
        </Popup>
    )
}