import { useState, useEffect } from 'react';
import Spinner from './widgets/Spinner';
import useAddressUtils from '../hooks/AddressUtils';

export default function PunkTradeHistory(props) {
    const [tradeHistory, setTradeHistory] = useState<any>(undefined)
    const { formatDate, formatAddressMini, addressLink, walletLink } = useAddressUtils();

    useEffect(() => {
        const fetchHistory = async () => {
            const res = await fetch("https://acy9375pq6.execute-api.us-east-1.amazonaws.com/shitPunkHistory?index="+props.punkId)
            const json = await res.json();
            const sorted = json.sort((a: any, b: any) => {
                return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
            });

            setTradeHistory(sorted);
            return json;
        }

        fetchHistory();
    }, [props.punkId, props.salt])

    if (!tradeHistory) {
        return (<Spinner></Spinner>)
    }

    

    return (
        <div className="page-punk-trade-history-list">
            <div>Date</div>
            <div>Event</div>
            <div>Amount</div>
            <div>Seller</div>
            <div>Buyer</div>
            {
                tradeHistory.map((row, index) => {
                    return (<>
                        <div key={"date"+row.createdAt} className="punk-history-date">{formatDate(row.createdAt)}</div>
                        <div key={"event"+row.createdAt} className="punk-history-event">{row.event}</div>
                        <div key={"price"+row.createdAt} className="punk-history-price">{
                            row.price !== undefined ? ( (<>
                                <img className="bnb-icon" src="/images/bnb_icon.png"/>
                                {row.price}
                            </>) ) : null
                        }</div>
                        { row.seller ? (
                            <div key={"seller"+row.createdAt} className="punk-history-address">
                                {walletLink(row.seller)} <a href={addressLink(row.seller)} rel="noreferrer" target="_blank">{formatAddressMini(row.seller)}</a> 
                            </div>
                        ) : <div key={"seller"+index}></div>}
                        
                        { row.buyer ? (
                            <div key={"buyer"+row.createdAt} className="punk-history-address">
                                {walletLink(row.buyer)} <a href={addressLink(row.buyer)} rel="noreferrer" target="_blank">{formatAddressMini(row.buyer)}</a> 
                            </div>
                        ) : <div key={"buyer"+index}></div>}
                        
                    </>
                    )
                })
            }
        </div>
               
    )
}