import Popup from "reactjs-popup";
import { useContext, useState } from "react";
import { Web3Context } from '../contexts/Web3Context';
import useAddressUtils from "../hooks/AddressUtils";
import Input from "../components/widgets/Input";
import Spinner from "../components/widgets/Spinner";
import Button from "../components/widgets/Button";

export default function SendTokenModal(props) {

    const [address, setAddress] = useState("")
    const [result, setResult] = useState<any>()
    const {web3, signedInAddress, writeContract, contracts} = useContext(Web3Context)
    const { txLink } = useAddressUtils()

    const signTransaction = async () => {
        if (!web3) {
            setResult({
                success: false,
                error: "Wallet is not connected"
            })
            return;
        }

        setResult({
            success: false,
            waiting: true
        })
        
        const result = await writeContract(contracts.shitpunks2, 'safeTransferFrom', [
            signedInAddress, 
            address,
            props.tokenId
        ]);

        setResult(result);
    }

    const render = (close: any) => {
        if (result && result.success) {
            return (<>
                <div>{props.metadata.name} has been sent.</div>
                <div>View <a rel="noreferrer" target="_blank" href={txLink(result.receipt.transactionHash)}>transaction here</a>.</div>
                <Button onClick={close} className="button button button-ok">Ok</Button>
            </>)
        } else if (result && result.waiting) {
            return <div><Spinner></Spinner></div>
        } else {
            return (
                <>
          
                <Input label="receiver address" onChange={setAddress} value={address}></Input>
                { (result && result.error) ? (
                    <>
                        <div className="error-text">{result.error}</div>
                    </>
                ) : null}
                <Button onClick={signTransaction} className="button">Send</Button>
                </>
            )
        }
    }

    return (
        <Popup
            nested
            modal
            onClose={() => {
                props.onClose()
            }}
            trigger={
                ({...props.children})
            }>
            {(close: any) => (<div className="modal send-token-modal">   
                    <Button className="button--modal-close" onClick={close}>&times;</Button>
                    <h3>Sending {props.metadata.name}</h3>
                    <div className="content">
                        {render(close)}
                    </div>
            </div>)
            }
        </Popup>
    )
}