export default function Marketplace(props) {
    const {rarityData} = useContext(CollectionInfoContext)
    const [searchParams, setSearchParams] = useSearchParams();
    const [showFilters, setShowFilters] = useState(false)
    const [fetchingMore, setFetchingMore] = useState(false)
    const [error, setError] = useState<any>(null)
    const {web3, contracts} = useContext(Web3Context)
    const [lastKnownBlock, setLastKnownBlock] = useState(null);
    const [lastSyncedBlock, setLastSyncedBlock] = useState(null);
    const [syncDelay, setSyncDelay] = useState<any>(null);

    const [data, setData] = useState<any>({
        lastFetchedPage: -1,
        loading: true,
        count: 0,
        tokens: []
    })

    const [fetchSalt, setFetchSalt] = useState(0)

    const fetchTokens = async (data) => {
        if (!web3 || !contracts.fetched) {
            return;
        }
        setError(null);
        setFetchingMore(true);

        console.log("fetchTokens", data);

        const queryParams = rarityData.traits.reduce((acc, cur) => {
            const value = searchParams.get(cur.name)
            if (value) {
                acc.push(`a_${cur.name}=${value}`)
            }
            return acc;
        }, [])

        const sortMode = searchParams.get('sort')
        if (sortMode) {
            //queryParams.push("price="+sortByPrice);
            queryParams.push("sort="+sortMode)
        } else {
            if (props.marketplaceMode == "all") {
                queryParams.push("sort=rank-high")
            }
        }

        const rank = searchParams.get('rank')
        if (rank) {
            queryParams.push("rank="+rank)
        }

        const skin = searchParams.get('skin')
        if (skin) {
            queryParams.push("skin="+skin)
        }

        const attrCount = searchParams.get('attrCount')
        if (attrCount) {
            queryParams.push("attrCount="+attrCount)
        }


        let queryString = queryParams.join('&')
        if (queryString.length > 0) {
            queryString = "&" + queryString;
        }
        
        const pageToFetch = data.lastFetchedPage+1;

        const apis = {
            activity: "marketplaceTrades",
            all: "allTokens"
        };
        const apiFunc = apis[props.marketplaceMode] || "marketplace";
        //const res = await fetch(`http://localhost:8083?${queryString}`)
        const res = await fetch(`https://acy9375pq6.execute-api.us-east-1.amazonaws.com/${apiFunc}?page=${pageToFetch}${queryString}`).catch(err => {
            console.log(err);
        })
        if (!res) {
            setError("Network error");
            setFetchingMore(false);

            setData({
                lastFetchedPage: pageToFetch,
                count: data.tokens.length,
                loading: false,
                tokens: [
                    ...data.tokens
                ]
            })
            return;
        }
        const json: any = await res?.json();

        const page = json.page || json[0].page;
        const withMetadata = page.map(item => {
            const currentTime = new Date().getTime();

            const getTimestamp = () => {
                if (!item.last || !item.last.timestamp) {
                    return undefined;
                }
                return (item.last.timestamp * 1000 > currentTime) ? currentTime : item.last.timestamp * 1000;
            }
            const timestamp = getTimestamp()

            const getLastPrice = () => {
                if (item.history && item.history.length >= 2) {
                    const prevListing = item.history[1];
                    if (prevListing.price) {
                        const prevPrice = web3.utils.fromWei(prevListing.price, 'ether');
                        return prevPrice;
                    }
                }

                return null;
            }
            

            const listingInfo = item.last ? {
                buyer: item.last.buyer,
                price: web3.utils.fromWei(item.last.price, 'ether'),
                prevListing: getLastPrice(),
                //punkID: item.last.tokenId,
                seller: item.last.seller,
                timestamp: timestamp,
                blockNumber: item.last.blockNumber,
                event: item.last.event,
                marketplace: item.last.address == contracts.pcs.address ? "pcs" : "shitmarket"
            } : {}

            return {
                metadata: item.metadata,
                rank: item.metadata.rank,
                image: item.metadata.image,
                punkID: item.metadata.id,
                ...listingInfo
            }
        })

        const numFound = json.numFound || json[0].numFound;
        const count = numFound.length > 0 ? numFound[0].numFound : 0;

        setFetchingMore(false);
        setLastSyncedBlock(json.lastSyncedBlock);
        setData({
            lastFetchedPage: pageToFetch,
            count: count,
            loading: false,
            tokens: [
                ...data.tokens,
                ...withMetadata
            ]
        })
    }

    useEffect(() => {
        if (!web3) {
            return;
        }
        web3.eth.getBlockNumber().then((res: any) => {
            setLastKnownBlock(res);
        })

    }, [web3])

    useEffect(() => {
        if (!web3) {
            return;
        }

        const fetchTimeStamps = async () => {
            const lastKnown: any = lastKnownBlock ? await web3.eth.getBlock(lastKnownBlock) : null;
            const lastSynced: any = lastSyncedBlock ? await web3.eth.getBlock(lastSyncedBlock) : null;
            if (lastSynced && lastKnown) {
                setSyncDelay({
                    syncDelayInSeconds: Math.max(0, lastKnown.timestamp - lastSynced.timestamp),
                    lastSyncedTime: lastSynced.timestamp
                })
            }
        }

        fetchTimeStamps();
    }, [web3, lastKnownBlock, lastSyncedBlock])

    useEffect(() => {
        setData({
            loading: true
        })

        fetchTokens({
            lastFetchedPage: -1,
            count: 0,
            loading: true,
            tokens: []
        })
       
    }, [contracts.fetched, web3, searchParams, fetchSalt])

    const getNetworkDelayString = () => {
        if (syncDelay.syncDelayInSeconds < 60) {
            return syncDelay.syncDelayInSeconds + " seconds"
        } else if (syncDelay.syncDelayInSeconds < 3600) {
            return Math.round(syncDelay.syncDelayInSeconds / 60) + " minutes"
        } else {
            return Math.round(syncDelay.syncDelayInSeconds / 3600) + " hours"
        }
    }

    return (
        <div className="page page-marketplace">
            <h1>ShitMarket<span>.</span></h1>

            <div className="marketplace-info">
                <div className="marketplace-description">
                5% of marketplace sales get contributed to the ShitPunks DAO. A community controlled fund that helps propel the project.
                </div>
                <MarketplaceStats />
            </div>
                        
            <PageMenuBar />

            <div className="marketplace-main">

                <PunkFilters visible={showFilters}
                    hideFilters={() => { setShowFilters(false) }}
                ></PunkFilters>

                <div className="punk-results-container">
                    <PunkSearchBar 
                        marketplaceMode={props.marketplaceMode} 
                        visible={!showFilters} 
                        showFilters={() => {
                            setShowFilters(true);
                        }}
                        refresh={() => {
                            setFetchSalt(fetchSalt + 1)
                        }}
                    />

{ syncDelay ? (
    <>
    <div className="network-delay">Network delay: {getNetworkDelayString()}</div>
    </>
) : null}

                    

                    { (data.loading) ? (
                        <Spinner></Spinner> 
                    ) : (
                        <>
                            <PunkCardList classSuffix="marketplace" total={data.count} tokens={data.tokens}></PunkCardList>
                            
                            { (fetchingMore) ? (
                                <Spinner></Spinner> 
                            ) : 
                                (data.tokens.length >= data.count) ? null : (
                                    <Button className="button button-fetch-more" onClick={() => { fetchTokens(data) }}>Load more</Button>
                                )
                            }

                            { error ? (
                                <div className="network-error">Network error :( Please try again later.</div>
                            ) : null}
                        </>                        
                    )}
                    
                </div>
            </div>
        </div>
    )
}
