import { useEffect, useState } from 'react';
import PageMenuBar from './PageMenuBar';
import Spinner from './widgets/Spinner';

function NFTCalendarDescription(props) {
    const [expanded, setExpanded] = useState(false);
    const words = props.text.split(" ");
    const first50 = words.splice(0, 30);
    if (words.length == 0) {
        return (first50.join(" "))
    } else {
        return (<>
            {first50.join(" ")}
            { expanded ? (
                <>
                    {props.text}
                    <div className="text-link" onClick={() => {setExpanded(false)}}>show less</div>
                </>
            ) : (
                <>
                    {first50.join(" ") + "..."}
                    <div className="text-link" onClick={() => {setExpanded(true)}}>show more</div>
                </>
            ) }
            
        </>)
    }

}

function NFTCalendarRow(props) {
    const socialLinks: any[] = [];
    const addSocialLink = (type, url) => {
        if (url) {
            socialLinks.push({
                type, 
                url
            })
        }
    }

    addSocialLink("web", props.url_web);
    addSocialLink("twitter", props.url_twitter);
    addSocialLink("discord", props.url_discord);
    addSocialLink("telegram", props.url_telegram);

    const socialIcons = {
        "twitter": "fab fa-twitter",
        "discord": "fab fa-discord",
        "web": "fas fa-home",
        "telegram": "fab fa-telegram",
    }

    const imageUrl = `https://shitpunks-nftdrops.s3.amazonaws.com/${props.image}`

    return (
        <>
            <a className="nft-calendar-image" target="_blank" href={props.url_web}>
                <img src={imageUrl}></img>
            </a>
            <div>
                <div className="nft-calendar-title">
                    <a target="_blank" href={props.url_web}>
                        {props.title}
                    </a>
                </div>
                <div className="nft-calendar-links">
                    { socialLinks.map(item => {
                        return <a rel="noreferrer" 
                            target="_blank" 
                            key={item.url} 
                            href={item.url}><i className={socialIcons[item.type]}></i></a>
                    })}
                </div>
            </div>
            <div className="nft-calendar-token">
                { props.mintPrice.length > 0 ? (
                    <div className="nft-calendar-token-price"><img className="bnb-icon" src="/images/bnb_icon.png"/> {props.mintPrice}</div>
                ) : null}
                <div>{props.totalSupply} tokens </div>
            </div>
            <div className="nft-calendar-description">
                <NFTCalendarDescription text={props.description}></NFTCalendarDescription>
            </div>
        </>
    )
}

export function NFTCalendar(props) {
    const [upcomingMints, setUpcomingMints] = useState<any[] | null>(null);
    const [pastMints, setPastMints] = useState<any[] | null>(null);

    useEffect(() => {
        setUpcomingMints(null)
        setPastMints(null)

        fetch('https://1crfk34q6g.execute-api.us-east-1.amazonaws.com/beta/nftdrops').then(async res => {
            const json: any = await res.json();
            
            const mints = json.reduce((byDate, nftDrop) => {
                const dateString = new Date(nftDrop.date).toDateString();
                if (!byDate[dateString]) {
                    byDate[dateString] = {
                        date: dateString,
                        dateTime: nftDrop.date,
                        drops: []
                    }
                }
                byDate[dateString].drops.push(nftDrop)
                return byDate;
            }, {})

            const currentTime = new Date().getTime();
            const tempUpcoming = Object.values(mints).filter((item: any) => item.dateTime >= currentTime)
            const tempPastMints = Object.values(mints).filter((item: any) => item.dateTime < currentTime)

            setUpcomingMints(tempUpcoming.sort((a: any, b: any) => a.dateTime - b.dateTime))
            setPastMints(tempPastMints.sort((a: any, b: any) => b.dateTime - a.dateTime))
        });
    }, [])

    const mintsToShow = props.mode == "released" ? pastMints : upcomingMints;

    return (
        <div className="page page-nft-calendar">
            <h1>NFT Drops<span>.</span></h1>

            <div className="nft-calendar-intro">
                <div className="nft-calendar-addtoken">
                    Do you want to list your project? Submit it <a href="/nftdrops/form">here</a>.
                </div>

                <div className="nft-calendar-disclaimer">
                    <i className="fa-2x fas fa-exclamation-circle"></i>
                    <div>
                        Listed projects are <span className="text-accent">not endorsed or verified by ShitPunks</span>.<br></br>
                        Do your own research before investing.
                    </div>
                </div>
            </div>

            <PageMenuBar links={[
                {
                    title: "Upcoming",
                    icon: "far fa-calendar",
                    link: "/nftdrops/upcoming"
                },
                {
                    title: "Released",
                    icon: "far fa-calendar-check",
                    link: "/nftdrops/released"
                }
            ]}></PageMenuBar>
            
            { mintsToShow === null ? <Spinner></Spinner> : null}

            { mintsToShow && mintsToShow.length === 0 ? (
                <div>No results</div>
            ) : null}

            <div className="nft-calendar-grid">
                { 
                    mintsToShow?.map(dateInfo => {
                        return <>
                            <div className="nft-calendar-date-row">
                                <td colSpan={4} className="nft-calendar-date">{dateInfo.date}</td>
                            </div>
                            {
                                dateInfo.drops.map(data => <NFTCalendarRow key={data.title} {...data}></NFTCalendarRow>)
                            }
                        </>
                    })
                    
                }
            </div>
            

        </div>
    )
}

