import { useEffect, useState } from "react"
import Spinner from "../widgets/Spinner";

function MarketplaceStatDiff(props: any) {
    const diff = parseFloat(props.diff);
    const variant = diff == 0 ? "neutral" : (diff > 0 ? "positive" : "negative");
    const value = (diff == 0 ? "" : diff > 0 ? "+" : "") + diff;
    return (
        <span className={`marketstat-diff marketstat-diff--${variant}`}>({ value }
            { props.verbose ? " past 24 hours" : "" })
        </span>        
    )
}

export default function MarkeplaceStats(props) {

    const [error, setError] = useState<any>(null)
    const [stats, setStats] = useState<any>({})

    useEffect(() => {
        setError(null);
        fetch("https://acy9375pq6.execute-api.us-east-1.amazonaws.com/marketplaceStats").then(async (res: any) => {
            const json = await res.json();
            setStats({
                ...stats,
                ...json
            })
        }).catch(err => {
            setError("Network error");
        })
    }, [])

    const getFloorDiff = () => {
        const floorToday = Math.min(stats.now.floor["shitmarket"], stats.now.floor["pcs"]);
        const floorYesterday = Math.min(stats.minus24h.floor["shitmarket"], stats.minus24h.floor["pcs"]);
        return parseFloat((floorToday - floorYesterday).toFixed(3));
    }

    const getVolumeDiff = () => {
        const volumeToday = stats.now.volume["shitmarket"] + stats.now.volume["pcs"];
        const volumeYesterday = stats.minus24h.volume["shitmarket"] + stats.minus24h.volume["pcs"];
        return parseFloat((volumeToday - volumeYesterday).toFixed(1));
    }
    
    const getHoldersDiff = () => {
        return stats.now.holdersCount - stats.minus24h.holdersCount;
    }
    
    return (
        <div className="marketplace-stats">
            <div className="marketplace-stats-boxes">
            <div className="marketplace-stat-box">
                <div className="marketplace-stat-label">Floor price</div>
                <div className="marketplace-stat-value">
                    { stats.now ? (
                        <div className="marketplace-stat-price">
                            <img className="bnb-icon" src="/images/bnb_icon.png" />
                            {(
                                Math.min(stats.now.floor["shitmarket"], stats.now.floor["pcs"])
                            ).toFixed(3)}
                            <MarketplaceStatDiff diff={getFloorDiff()}></MarketplaceStatDiff>
                        </div>
                    ) : <Spinner small={true}></Spinner>}
                </div>
                { stats.now ? (
                <div className="extra-info">
                    <h3>Floor prices</h3>
                    ShitMarket: {(0+stats.now.floor['shitmarket']).toFixed(3)} BNB <MarketplaceStatDiff 
                        verbose={true} 
                        diff={(stats.now.floor["shitmarket"] - stats.minus24h.floor["shitmarket"]).toFixed(3)}
                    ></MarketplaceStatDiff><br></br>
                    
                    PancakeSwap: {(0+stats.now.floor['pcs']).toFixed(3)} BNB <MarketplaceStatDiff 
                        verbose={true} 
                        diff={(stats.now.floor["pcs"] - stats.minus24h.floor["pcs"]).toFixed(3)}
                    ></MarketplaceStatDiff><br></br>
                </div>
                ) : null}
            </div>


            <div className="marketplace-stat-box">
                <div className="marketplace-stat-label">Volume</div>
                <div className="marketplace-stat-value">
                    { stats.now ? (
                                    <div className="marketplace-stat-price">
                                    <img className="bnb-icon" src="/images/bnb_icon.png" />
                                        {(
                                            stats.now.volume["shitmarket"] + stats.now.volume["pcs"]
                                        ).toFixed(0)}
                                        <MarketplaceStatDiff diff={getVolumeDiff()}></MarketplaceStatDiff>
                                    </div>
                    ) : <Spinner small={true}></Spinner>}
                </div>

                { stats.now ? (
                <div className="extra-info">
                    <h3>Total volume</h3>
                    ShitMarket: {(0+stats.now.volume['shitmarket']).toFixed(0)} BNB <MarketplaceStatDiff 
                        verbose={true} 
                        diff={ (stats.now.volume['shitmarket'] - stats.minus24h.volume['shitmarket']).toFixed(1) }
                    ></MarketplaceStatDiff><br></br>
                    PancakeSwap: {(0+stats.now.volume['pcs']).toFixed(0)} BNB <MarketplaceStatDiff 
                        verbose={true} 
                        diff={ (stats.now.volume['pcs'] - stats.minus24h.volume['pcs']).toFixed(1) }
                    ></MarketplaceStatDiff><br></br>
                </div>
                ) : null }
                
            </div>   

            <div className="marketplace-stat-box">
                <div className="marketplace-stat-label"># Holders</div>
                <div className="marketplace-stat-value">
                    { stats.now ? (
                        <div className="marketplace-stat-price">
                        {stats.now.holdersCount}
                        <MarketplaceStatDiff diff={getHoldersDiff()}></MarketplaceStatDiff>
                        </div>
                    ) : <Spinner small={true}></Spinner>}
                </div>
                { stats.now ? (
                <div className="extra-info">
                    <h3>Holders</h3>
                    Number of unique addresses holding a shitpunk: {stats.now.holdersCount}
                    <MarketplaceStatDiff verbose={true} diff={getHoldersDiff()}></MarketplaceStatDiff>
                </div>
                ) : null }
            </div>   
            </div>


            
       
        </div>
    )
}