import { useState, useContext, useEffect } from "react"
import SelectField from "./widgets/SelectField"
import { CollectionInfoContext } from "../contexts/CollectionInfoContext"
import { useSearchParams } from "react-router-dom";
import Button from "./widgets/Button";
import { Web3Context } from "../contexts/Web3Context";

export default function PunkFilters(props) {
    const {rarityData} = useContext(CollectionInfoContext)
    const {web3} = useContext(Web3Context)
    const [searchParams, setSearchParams] = useSearchParams();
    const [attributeFloor, setAttributeFloor] = useState<any>({});
    const [showFloorPrices, setShowFloorPrices] = useState<boolean>(true);

    const handleChange = (event) => {
        let params = searchParams;
        if (event.value == null) {
            params.delete(event.type)
        } else {
            params.set(event.type, event.value);
        }
        
        setSearchParams(params, { replace: false })
    }

    useEffect(() => {
        const fetchAttributeFloor = async () => {
            const res = await fetch("https://acy9375pq6.execute-api.us-east-1.amazonaws.com/marketplaceAttributeFloor");
            const json = await res.json();
            setAttributeFloor(json);
        }

        fetchAttributeFloor();
    }, [])

    const formatFloorPrice = (floorPriceString) => {
        const floorPriceBnb = floorPriceString ? web3?.utils.fromWei(floorPriceString) : undefined;
        const floorPriceBnbFloat = floorPriceBnb ? parseFloat(parseFloat(floorPriceBnb).toFixed(2)) : undefined;
        return floorPriceBnbFloat;
    }

    const fields = rarityData.traits.map(trait => {
        const values = trait.values.map(value => {
            const floorType = attributeFloor.attributes ? attributeFloor.attributes[trait.name] : undefined;
            const floorPrice = floorType ? floorType[value.name] : undefined;
            
            return {
                label: value.name,
                value: value.name,
                count: value.count,
                floor: formatFloorPrice(floorPrice)
            }
        }).sort((a,b) => {
            if (showFloorPrices) {
                return b.floor - a.floor;
            }
            return a.count - b.count;
        })
        const searchParam = searchParams.get(trait.name)
        const defaultValue = values.find(val => val.label == searchParam)
        return (<SelectField 
            key={trait.name + defaultValue}
            placeholder={trait.name} 
            isClearable={true} 
            name={trait.name} 
            onChange={handleChange}
            options={values}
            defaultValue={defaultValue}
            showFloorPrices={showFloorPrices}
        />)
    })
    
    const skins = [
        {
            label: "Albino",
            value: "Albino",
            count: 1018
        },
        {
            label: "Light",
            value: "Light",
            count: 3006
        },
        {
            label: "Mid",
            value: "Mid",
            count: 3031
        },
        {
            label: "Dark",
            value: "Dark",
            count: 2824
        },
        {
            label: "Zombie",
            value: "Zombie",
            count: 88
        },
        {
            label: "Ape",
            value: "Ape",
            count: 24
        },
        {
            label: "Alien",
            value: "Alien",
            count: 9
        },
    ].map(skin => {
        return {
            ...skin,
            floor: formatFloorPrice(attributeFloor.skins ? attributeFloor.skins[skin.value] : undefined)
        }
    })

    const ranks = [
        {
            label: "Legendary",
            value: "legendary",
            count: 100
        },
        {
            label: "Epic",
            value: "epic",
            count: 900
        },
        {
            label: "Rare",
            value: "rare",
            count: 1500
        },
        {
            label: "Uncommon",
            value: "uncommon",
            count: 2500
        },
        {
            label: "Common",
            value: "common",
            count: 5000
        }
    ].map(rank => {
        return {
            ...rank,
            floor: formatFloorPrice(attributeFloor.skins ? attributeFloor.ranks[rank.value] : undefined)
        }
    })
    const searchRank = searchParams.get('rank')
    const searchAttrCount = searchParams.get('attrCount')
    const searchSkin = searchParams.get('skin');
    
    const attrCountFields = [...Array(8).keys()].map((x, index) => {
        const counts = [8, 333, 3560, 4501, 1420, 166, 11, 1]
        return {
            label: index + " Attributes",
            value: index,
            count: counts[index],
            floor: formatFloorPrice(attributeFloor.attrCount ? attributeFloor.attrCount[index] : undefined)
        }
    })

    const className = `punk-filters ` + (props.visible ? "punk-filters--visible" : "")
    return (<div className={className}>
        <div className="punk-filters-header">
            <div><i className="fas fa-filter"/>&nbsp;Filters</div>
        </div>
        <div className="toggle-filters">
            <div><i className="fas fa-filter"/>&nbsp;Filters</div>
            <Button className="button--modal-close" onClick={props.hideFilters}>&times;</Button>
        </div>

        <div className="filter-checkbox" onClick={() => {setShowFloorPrices(!showFloorPrices)}}>
            <input type="checkbox" checked={showFloorPrices}></input> Show floor prices 
        </div>

        <hr></hr>
        
        <SelectField 
            placeholder="Num attributes" 
            isClearable={true} 
            name="attrCount" 
            onChange={handleChange}
            defaultValue={attrCountFields.find((item: any) => item.value == searchAttrCount)}
            options={attrCountFields}
            showFloorPrices={showFloorPrices}
        />
               
        <SelectField 
            placeholder="Rarity" 
            isClearable={true} 
            name="rank" 
            onChange={handleChange}
            defaultValue={ranks.find(item => item.value == searchRank)}
            options={ranks}
            showFloorPrices={showFloorPrices}
        />

        <SelectField 
            placeholder="Skin" 
            isClearable={true} 
            name="skin" 
            onChange={handleChange}
            defaultValue={skins.find(item => item.value == searchSkin)}
            options={skins}
            showFloorPrices={showFloorPrices}
        />

        <hr/>

        {fields}

    <div className="search-button" style={{width: "100%"}}>
        <Button onClick={props.hideFilters} style={{margin: "auto"}} className="button">Search</Button>
    </div>
        
    </div>)
}