import { useEffect } from 'react';
import { useParams } from 'react-router';
import { useState, useContext } from 'react';
import Spinner from './widgets/Spinner';
import { CollectionInfoContext } from '../contexts/CollectionInfoContext';
import PunkTradeHistory from './PunkTradeHistory';
import { Web3Context } from '../contexts/Web3Context';
import useAddressUtils from '../hooks/AddressUtils';
import ConnectWrapper from './widgets/ConnectWrapper';
import ListOnMarketModal from '../modals/ListOnMarketModal';
import AdjustPriceModal from '../modals/AdjustPriceModal';
import BuyPunkModal from '../modals/BuyPunkModal';
import Button from './widgets/Button';
import SendTokenModal from '../modals/SendTokenModal';
import tokenIdMap from '../data/tokenid_punkid_map.json';

const getRarityImage = (rank) => {
    if (rank < 100) {
        return "legendary"
    } else if (rank < 1000) {
        return "epic"
    } else if (rank < 2500) {
        return "rare"
    } else if (rank < 5000) {
        return "uncommon"
    } else {
        return "common"
    } 
}

export default function PunkDetails(props) {
    let { tokenId, punkId } = useParams();

    if (punkId !== undefined) {
        Object.keys(tokenIdMap).forEach((_tokenId: string, index: number) => {
            const _punkId = tokenIdMap[_tokenId];
            if (_punkId == punkId) {
                tokenId = ""+_tokenId;
            }
        })
    }

    const [metadata, setMetadata] = useState<any>(undefined)
    const [owner, setOwner] = useState<any>(undefined)
    const [sellInfo, setSellInfo] = useState<any>(undefined)
    const [ownedByYou, setOwnedByYou] = useState<boolean>(false)
    const [marketplaceError, setMarketplaceError] = useState<any>(undefined)
    const [waitingForTransaction, setWaitingForTransaction] = useState(false)
    const {rarityData} = useContext(CollectionInfoContext)
    const {signedInAddress, web3, contracts, writeContract} = useContext(Web3Context)
    const [tradehistorySalt, setTradeHistorySalt] = useState(1)
    const { formatDate, formatAddress, addressLink, walletLink } = useAddressUtils();

    const fetchOwner = async () => {
        if (!web3) {
            setOwner(undefined);
            setSellInfo(undefined)
            return;
        }

        setWaitingForTransaction(true);
        
        const contractOld = new web3.eth.Contract(contracts.shitpunks.abi, contracts.shitpunks.address)
        let owner = await contractOld.methods.ownerOf(tokenId).call()


        if (owner == contracts.shitpunks2.address) {
            const contract = new web3.eth.Contract(contracts.shitpunks2.abi, contracts.shitpunks2.address)
            owner = await contract.methods.ownerOf(tokenId).call()
            setOwner(owner);
        } else if (owner != contracts.pcs.address) {
            setOwner(owner);
        }

        if (owner == contracts.pcs.address) {
            const contractPcs = new web3.eth.Contract(contracts.pcs.abi, contracts.pcs.address)
            const sellInfo = await contractPcs.methods.viewAsksByCollectionAndTokenIds(contracts.shitpunks2.address, [tokenId]).call()
            setOwner(sellInfo.askInfo[0].seller);
            
            const price = web3.utils.fromWei(sellInfo.askInfo[0].price, 'ether')
            setSellInfo({
                price: price,
                marketplace: "PancakeSwap",
                marketplaceLink: `https://pancakeswap.finance/nfts/collections/0x11304895f41C5A9b7fBFb0C4B011A92f1020EF96/${tokenId}`
            })
        } else {
            const contractShitMarket = new web3.eth.Contract((contracts.shitmarket as any).abi, contracts.shitmarket.address)
            const listing = await contractShitMarket.methods.getListing(tokenId).call()
            if (listing[4] && listing[0].toLowerCase() == owner.toLowerCase()) {
                setSellInfo({
                    price: web3.utils.fromWei(listing[3], 'ether'),
                    marketplace: "Shitmarket"
                })
            } else {
                setSellInfo(undefined)
            }
        }

        setWaitingForTransaction(false);
    }

    useEffect(() => {
        if (!contracts.fetched) {
            return;
        }

        const fetchMetadata = async () => {
            const res = await fetch('https://acy9375pq6.execute-api.us-east-1.amazonaws.com/shitPunkMeta2?index='+tokenId)
            const json = await res.json();

            setMetadata(json);
            return json;
        }

        fetchMetadata()
        fetchOwner()
        
    }, [tokenId, web3, contracts.fetched])

    useEffect(() => {
        const owned = !!(signedInAddress && owner && signedInAddress.length > 0 && (signedInAddress.toLowerCase() == owner.toLowerCase()));
        setOwnedByYou(owned);
    }, [signedInAddress, owner])

    if (!metadata) {
        return (
            <div><Spinner></Spinner></div>
        )
    }
    
    const rarityImage = '/images/ranks/' + getRarityImage(metadata.rank) +'.png';

    const getTraitRarity = (trait) => {
        const traitInfo = rarityData.traits.find(t => t.name == trait.trait_type)
        const valueInfo = traitInfo.values.find(v => v.name == trait.value)
        return valueInfo.count;
    }

    const attributes = metadata.attributes.map(attr => {
        const rarity = getTraitRarity(attr);
        const rarityPercent = parseFloat((rarity / 100).toFixed(2))+"%";
        return {
            ...attr,
            rarity: rarity,
            rarityPercent: rarityPercent
        }
    }).sort((a,b) => a.rarity - b.rarity)

    

    const cancelListing = async () => {
        setWaitingForTransaction(true)
        setMarketplaceError(undefined)

        const cancelListingFromMarketPlace = async () => {
            if (sellInfo.marketplace == 'PancakeSwap') {
                return await writeContract(contracts.pcs, "cancelAskOrder", [contracts.shitpunks2.address, tokenId]);
            } else {
                return await writeContract(contracts.shitmarket, "cancelListing", [tokenId]);
            }
        }

        const result = await cancelListingFromMarketPlace();
        
        console.log("cancelListing done", result);
        if (result.success) {
            setTradeHistorySalt(tradehistorySalt + 1);
            await fetchOwner();
        } else {
            setMarketplaceError(result.error);
        }

        setWaitingForTransaction(false)
    }

    return (
        <>
        
        <div className="page page-punk">
            <h1>Shit Punk <span>{metadata.id}</span></h1>
            <div className="page-punk-main">
                <div className="page-punk-column-left">
                    <div className="punk-details-image-container">
                        <img className="punk-image" src={metadata.image}></img>
                        <img className='punk-details-card-rarity' src={rarityImage} alt='Rarity'></img>                    
                        { (metadata.attributes.length <= 2 || metadata.attributes.length >= 6) ? (
                            <div className='punk-card-attrcount'>{metadata.attributes.length-1} attr</div>
                        ) : null }
                    </div>
                </div>
                <div className="page-punk-column-right">


                    <h2>Current price</h2>
                    { sellInfo ? (
                        <>
                        <div className="attributes">
                            <div className={"attribute-box"} style={{flexDirection: "row", alignItems: "center"}}>
                                <img style={{height: "2rem", marginRight: ".5rem"}} src="/images/bnb_icon.png" />{sellInfo.price} 
                                &nbsp;<a href={sellInfo.marketplaceLink} target='_blank' rel="noreferrer">on {sellInfo.marketplace}</a>
                            </div>
                        </div>
                        { ownedByYou ? (
                            <div style={{display: "flex", gap: "1rem", width: "100%"}}>
                                { waitingForTransaction ? (
                                    <Spinner></Spinner>
                                ) : (
                                    <>
                                    <AdjustPriceModal 
                                        onClose={() => {
                                            setTradeHistorySalt(tradehistorySalt + 1);
                                            fetchOwner()
                                        }}
                                        metadata={metadata} 
                                        tokenId={tokenId}
                                        price={sellInfo.price}
                                        sellInfo={sellInfo}
                                    >
                                        <Button className="button">Adjust price</Button>
                                    </AdjustPriceModal>

                                    <Button onClick={cancelListing} className="button">Cancel listing</Button>
                                    </>
                                )}
                                
                            </div>
                            
                        ) : (
                            <ConnectWrapper connectText="Connect wallet to buy">
                                <BuyPunkModal 
                                    onClose={() => {
                                        setTradeHistorySalt(tradehistorySalt + 1);
                                        fetchOwner()
                                    }}
                                    metadata={metadata} 
                                    tokenId={tokenId}
                                    price={sellInfo.price}
                                    sellInfo={sellInfo}
                                >
                                    <Button className="button button--green">Buy</Button>
                                </BuyPunkModal>
                            </ConnectWrapper>
                        )}
                        </>
                    ) : (
                        <>
                        <div className="attributes">
                            <div className="attribute-box">
                                Not for sale
                            </div>
                        </div>                        
                        { ownedByYou ? (
                            <>
                            <ListOnMarketModal 
                                metadata={metadata} 
                                tokenId={tokenId} 
                                onChange={fetchOwner}
                            >
                                <Button className="button">List on ShitMarket</Button>
                            </ListOnMarketModal>
                            <SendTokenModal
                                metadata={metadata} 
                                tokenId={tokenId} 
                                onClose={fetchOwner}>
                                <Button className="button">Send</Button>
                            </SendTokenModal>
                            </>
                        ) : null}
                        </>
                    )}

                    { (marketplaceError) ? <div className="error-text">{marketplaceError}</div> : null}
                  
                    <br />

                    <h2>Details</h2>
                    <div className="attributes">
                        <div className={"attribute-box"}>
                            <div className="attribute-box-type">Token id</div>
                            <div className="attribute-box-value">{tokenId}</div>
                        </div>
                        <div className={"attribute-box"}>
                            <div className="attribute-box-type">Rank</div>
                            <div className="attribute-box-value">{metadata.rank}</div>
                        </div>
                    </div>



                    <h2>Attributes</h2>
                    <div className="attributes">
                    {attributes.map(attr => {
                        const extraClassName = attr.trait_type == "Type" ? " attribute-box--type" : "";
                        return (
                            <div className={"attribute-box" + extraClassName} key={attr.trait_type}>
                                <div className="attribute-box-type">{attr.trait_type}</div>
                                <div className="attribute-box-value">{attr.value}</div>
                                <div className="attribute-box-rarity">{attr.rarityPercent}</div>
                            </div>
                        )
                    })}
                    </div>


                    <h2>Owner</h2>
                    <div className="attributes">
                        <div className={"attribute-box"} style={{flexDirection: "row"}}>
                            { walletLink(owner) } 
                            <a href={addressLink(owner)} target="_blank" rel="noreferrer">
                                { formatAddress(owner) }
                            </a>
                        </div>
                    </div>

                
                </div>
            </div>

            <div className="page-punk-trade-history">
                <h3>Trade history</h3>
                <div style={{color: "#666"}}>Note! Some trades are missing from this list. This will be addressed soon.</div>
                <PunkTradeHistory salt={tradehistorySalt} punkId={metadata.id}></PunkTradeHistory>
            </div>
            
        </div>
        </>
    )
}
